import { VFC } from 'react';
import MultipleSelectPresenter from './MultipleSelectPresenter';

type NameValueItem = {
  name: string;
  value: string;
};

type Props = {
  items: NameValueItem[];
  label?: string;
  values?: string[];
  onChange?: (values: NameValueItem[]) => void;
};

const MultipleSelect: VFC<Props> = ({
  items,
  label = '',
  values = [],
  onChange,
}) => (
  <MultipleSelectPresenter
    items={items}
    label={label}
    values={values}
    onChange={onChange}
  />
);

export default MultipleSelect;
