import { VFC, useState } from 'react';
import {
  Button,
  Card,
  CardContent,
  CardHeader,
  IconButton,
  Typography,
  Divider,
  Grid,
} from '@mui/material';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import PatientCard from '../PatientCard/PatientCard';
import TransfusionExecute from './TransfusionPatientCardTypes';

type Props = {
  patientId: string;
  summary: string;
  status: string;
  executes: TransfusionExecute[];
};

type DisplayType = 'summary' | 'status' | 'execute' | '';

const TransfusionPatientCardPresenter: VFC<Props> = ({
  patientId,
  summary,
  status,
  executes,
}) => {
  const [expanded, setExpanded] = useState<boolean>(false);
  const [displayType, setDisplayType] = useState<DisplayType>('');

  const handleExpandClick = (selectedDisplayType: DisplayType) => {
    if (!displayType) {
      setExpanded(!expanded);
      setDisplayType(selectedDisplayType);

      return;
    }

    if (displayType !== selectedDisplayType) {
      setDisplayType(selectedDisplayType);

      if (!expanded) {
        setExpanded(!expanded);
      }

      return;
    }

    setExpanded(!expanded);
  };

  const createExecuteSummary = (
    execute: TransfusionExecute,
  ) => `製造番号：${execute.formulationNo}
  使用日：${execute.executeDate}
  実施者：${execute.executeStaffName}
  実施区分：${execute.executeTypeName}
  フィルタ：${execute.filterTypeName}
  副作用：${execute.sideEffectNames}
  `;

  const createCardContent = (inputDisplayType: DisplayType) => {
    if (inputDisplayType === 'summary') {
      return summary
        .split(/(\n)/)
        .filter((item) => /\n/.exec(item) === null)
        .map((item, index) => (
          <Typography key={`summary-${String(index)}`}>{item}</Typography>
        ));
    }

    if (inputDisplayType === 'status') {
      return status
        .split(/(\n)/)
        .filter((item) => /\n/.exec(item) === null)
        .map((item, index) => (
          <Typography key={`status-${String(index)}`}>{item}</Typography>
        ));
    }

    if (inputDisplayType === 'execute') {
      return executes.map((item) => (
        <Card variant="outlined" key={item.formulationNo}>
          <CardHeader
            avatar={
              item.isExecuteTarget ? (
                <IconButton aria-label="execute">
                  <ArrowForwardIcon fontSize="medium" />
                </IconButton>
              ) : (
                <IconButton aria-label="execute" sx={{ opacity: 0 }}>
                  <ArrowForwardIcon fontSize="medium" />
                </IconButton>
              )
            }
            title={
              <>
                <Typography
                  variant="h5"
                  component="div"
                  key={item.formulationName}
                >
                  {item.formulationName}
                </Typography>
              </>
            }
          />
          <CardContent>
            {createExecuteSummary(item)
              .split(/(\n)/)
              .filter((exec) => /\n/.exec(exec) === null)
              .map((exec, index) => (
                <Typography key={`content-${String(index)}`}>{exec}</Typography>
              ))}
          </CardContent>
        </Card>
      ));
    }

    return null;
  };

  // cardActions
  const cardActions: JSX.Element = (
    <Grid container spacing={2}>
      <Grid item xs={4}>
        <Button size="small" onClick={() => handleExpandClick('summary')}>
          {expanded && displayType === 'summary'
            ? 'オーダ内容を閉じる'
            : 'オーダ内容を表示'}
        </Button>
      </Grid>
      <Grid item xs={4}>
        <Button size="small" onClick={() => handleExpandClick('status')}>
          {expanded && displayType === 'status'
            ? '受付内容を閉じる'
            : '受付内容を表示'}
        </Button>
      </Grid>
      <Grid item xs={4}>
        <Button size="small" onClick={() => handleExpandClick('execute')}>
          {expanded && displayType === 'execute'
            ? '実施内容を閉じる'
            : '実施内容を表示'}
        </Button>
      </Grid>
    </Grid>
  );

  // cardContent
  const cardContent: JSX.Element = (
    <>
      <Divider />
      <CardContent>{createCardContent(displayType)}</CardContent>
    </>
  );

  return (
    <>
      <PatientCard
        patientId={patientId}
        cardActions={cardActions}
        cardContent={cardContent}
        displayContent={expanded}
      />
    </>
  );
};

export default TransfusionPatientCardPresenter;
