// eslint-disable-next-line no-unused-vars
import { VFC, useState, useEffect } from 'react';
import Quagga from '@ericblade/quagga2';
import AlertDialog from '../../molecules/ErrorDialog/AlertDialog';

type Props = {
  onScan?: (code: string) => void;
};

const BarcodeReader: VFC<Props> = ({ onScan }) => {
  const [scanResult, setScanResult] = useState<string>('');
  const [sameScanCount, setSameScanCount] = useState<number>(0);
  const [open, setOpen] = useState<boolean>(false);

  const handleDetected = (data: { codeResult: { code: string | null } }) => {
    const { code } = data.codeResult;

    if (code == null) {
      return;
    }

    if (code === scanResult) {
      setSameScanCount((c) => c + 1);
    }

    setScanResult((before) => {
      if (code === before) {
        setSameScanCount((c) => c + 1);
      }

      return code;
    });
    setOpen(false);

    // setOpen(false);
  };

  useEffect(() => {
    if (onScan && sameScanCount >= 3) {
      // 読み取り
      setSameScanCount(0);
      onScan(scanResult);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sameScanCount]);

  useEffect(() => {
    void Quagga.init(
      {
        inputStream: {
          type: 'LiveStream',
          constraints: {
            width: { min: 480 },
            height: { min: 200 },
            facingMode: 'environment',
            aspectRatio: { min: 1, max: 1 },
          },
        },
        locator: {
          patchSize: 'medium',
          halfSample: false,
        },
        numOfWorkers: 2,
        frequency: 10,
        decoder: {
          readers: ['codabar_reader', 'ean_reader', 'code_128_reader'],
        },
        locate: true,
      },
      (_) => {
        // if (err) {
        //   console.log(err, 'error msg');
        // }

        Quagga.start();

        return () => {
          void Quagga.stop();
        };
      },
    );

    Quagga.onDetected(handleDetected);

    return () => {
      void Quagga.stop();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    // If you do not specify a target,
    // QuaggaJS would look for an element that matches
    // the CSS selector #interactive.viewport
    <>
      <div id="interactive" className="viewport" />
      <p>{scanResult || '読み取り中...'}</p>
      <AlertDialog
        message={scanResult || 'empty'}
        alertOpen={open}
        onEmpty={() => setOpen(false)}
        toggleAlert={() => setScanResult('')}
      />
    </>
  );
};

export default BarcodeReader;
