import { FC, useState } from 'react';
import {
  Card,
  Typography,
  CardContent,
  Grid,
  IconButton,
  Dialog,
  Box,
  Divider,
} from '@mui/material/';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import CloseIcon from '@mui/icons-material/Close';
import { format } from 'date-fns';
import DropExecuteItemInput from './DropExecuteItemInput';
import { InjectionExecute } from '@/types/order/injection/injectionExecute';
import { InjectionOrder } from '@/types/order/injection/injectionOrder';

type Props = {
  itemData: InjectionExecute;
  onClickDeleteButton: (value: InjectionExecute) => void;
  itemDataArray: InjectionExecute[];
  injectionOrder: InjectionOrder;
  isLastRow: boolean;
};

const DropExecuteItem: FC<Props> = ({
  itemData,
  onClickDeleteButton,
  itemDataArray,
  isLastRow,
  injectionOrder,
}) => {
  const [updateInputDialog, setUpdateInputDialog] = useState(false);

  const [inputData, setInputData] = useState<InjectionExecute>(itemData);

  const openUpdateInputDialog = () => {
    setUpdateInputDialog(!updateInputDialog);
  };

  const setInputDataValue = (value: InjectionExecute) => {
    setInputData(value);
    openUpdateInputDialog();
  };

  return (
    <>
      <Card sx={{ minWidth: 275 }}>
        <CardContent>
          <Typography sx={{ fontSize: 16 }}>
            開始日時：
            {inputData.executionStartDate !== undefined
              ? format(
                  new Date(inputData.executionStartDate),
                  'yyyy/MM/dd HH:mm',
                )
              : ''}
          </Typography>
          <Typography sx={{ fontSize: 16 }}>
            終了日時：
            {inputData.executionEndDate !== undefined &&
            inputData.executionEndDate !== null
              ? format(new Date(inputData.executionEndDate), 'yyyy/MM/dd HH:mm')
              : ''}
          </Typography>
          <Typography sx={{ fontSize: 16 }}>
            速度：{inputData.executionDropSpeed}
          </Typography>
          <Typography sx={{ fontSize: 16 }}>
            投与量：{inputData.executionAmount}
          </Typography>
          <Typography sx={{ fontSize: 16 }}>
            コメント：{inputData.executionFreeComment}
          </Typography>
        </CardContent>
        {isLastRow && (
          <Grid container>
            <Grid item>
              <IconButton
                aria-label="delete"
                onClick={() => {
                  onClickDeleteButton(itemData);
                }}
              >
                <DeleteIcon />
              </IconButton>
            </Grid>
            <Grid item>
              <IconButton aria-label="edit" onClick={openUpdateInputDialog}>
                <EditIcon />
              </IconButton>
              <Dialog open={updateInputDialog} onClose={openUpdateInputDialog}>
                <Box sx={{ flexGrow: 1 }}>
                  <Grid container spacing={2}>
                    <Grid item xs={4}>
                      <IconButton onClick={openUpdateInputDialog}>
                        <CloseIcon />
                      </IconButton>
                    </Grid>
                    <Grid item xs={7}>
                      <Typography sx={{ fontSize: 25 }}>
                        点滴履歴編集
                      </Typography>
                    </Grid>
                  </Grid>
                </Box>
                <Divider />
                <DropExecuteItemInput
                  defaultExecute={inputData}
                  onInput={setInputDataValue}
                  executes={itemDataArray}
                  isShowRemainAmount={false}
                  totalAmount={injectionOrder.totalAmount}
                  reserveDate={injectionOrder.reserveDate}
                />
              </Dialog>
            </Grid>
          </Grid>
        )}
      </Card>
    </>
  );
};
export default DropExecuteItem;
