import { VFC, useEffect, useState } from 'react';
import ky from 'ky';
import PatientCardPresenter from './PatientCardPresenter';
import Patient from '../../../types/patient';

type Props = {
  patientId: string;
  cardActions?: JSX.Element;
  cardContent?: JSX.Element;
  displayContent?: boolean;
};

const PatientCard: VFC<Props> = ({
  patientId,
  cardActions,
  cardContent,
  displayContent,
}) => {
  const [patient, setPatient] = useState<Patient | undefined>(undefined);

  useEffect(() => {
    const loadPatient = async (): Promise<void> => {
      try {
        const patientResult = await ky
          .get(`http://localhost:8080/jerseywebapp/rest/patients/${patientId}`)
          .json<Patient>();

        setPatient(patientResult);
      } catch (err) {
        // eslint-disable-next-line no-console
        console.log('error');
      } finally {
        // setIsLoading(false);
      }
    };

    void loadPatient();
  }, [patientId]);

  return (
    <PatientCardPresenter
      patient={patient}
      cardActions={cardActions}
      cardContent={cardContent}
      displayContent={displayContent}
    />
  );
};

export default PatientCard;
