import { VFC } from 'react';
import TransfusionPatientCardPresenter from './TransfusionPatientCardPresenter';
import TransfusionExecute from './TransfusionPatientCardTypes';

type Props = {
  patientId: string;
  summary: string;
  status: string;
  executes: TransfusionExecute[];
};

const TransfusionPatientCard: VFC<Props> = ({
  patientId,
  summary,
  status,
  executes,
}) => (
  <TransfusionPatientCardPresenter
    patientId={patientId}
    summary={summary}
    status={status}
    executes={executes}
  />
);

export default TransfusionPatientCard;
