import { VFC } from 'react';
import Header from '../../oraganisms/Header/Header';
import LauncherMenuList from '../../oraganisms/LauncherMenuList/LauncherMenuList';
import SideBar from '../../oraganisms/SideBar/SideBar';

type Props = {
  drawerOpen: boolean;
  toggleDrawer: () => void;
  onLogout: () => void;
};

const MainMenuPresenter: VFC<Props> = ({
  drawerOpen,
  toggleDrawer,
  onLogout,
}) => (
  <>
    <Header onDrawerToggle={toggleDrawer} />
    <LauncherMenuList />
    <SideBar
      drawerOpen={drawerOpen}
      toggleDrawer={toggleDrawer}
      onOpen={onLogout}
    />
  </>
);

export default MainMenuPresenter;
