import { VFC } from 'react';
import {
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Select,
} from '@mui/material';
import TransfusionExecuteType from '../../../types/order/transfusion/transfusionExecuteType';

type Props = {
  execTypeItems: TransfusionExecuteType[];
  handleOnChange: (input: string | null) => void;
  defaultExecuteType?: string;
  label?: string;
  error?: boolean;
  helperText?: string;
};

const TransfusionExecuteTypeSelectPresenter: VFC<Props> = ({
  execTypeItems,
  handleOnChange,
  defaultExecuteType,
  label = '',
  error,
  helperText,
}) => (
  <>
    <FormControl error={error}>
      <InputLabel id="exec-type-label">{label}</InputLabel>
      <Select
        labelId="exec-type-label"
        id="exec-type"
        label="実施区分"
        value={defaultExecuteType}
        // defaultValue={defaultExecuteType}
        onChange={(event) => handleOnChange(event.target.value)}
        sx={{ width: 250 }}
      >
        {execTypeItems.map((item) => (
          <MenuItem value={item.value} key={item.value}>
            {item.name}
          </MenuItem>
        ))}
      </Select>
      <FormHelperText>{helperText}</FormHelperText>
    </FormControl>
  </>
);
export default TransfusionExecuteTypeSelectPresenter;
