import { VFC, useEffect, useState } from 'react';
import ky from '@/lib/ky';
import StaffSelectPresenter from './StaffSelectPresenter';
import Staff from '../../../types/staff';
import { useAuthUser } from '../../../contexts/auth/AuthUserContext';

type Props = {
  onChange?: (staff?: Staff) => void;
  label?: string;
  defaultStaffId?: number;
  error?: boolean;
  helperText?: string;
  disabled?: boolean;
};

type StaffResult = {
  staffs: Staff[];
};

const getStaffName = (staff: Staff): string => {
  const { staffId, firstName, lastName } = staff;

  return `${lastName} ${firstName} (ID:${staffId})`;
};

const getStaffNameById = (staffs: Staff[], staffId?: number): string => {
  if (!staffId) {
    return '';
  }

  const targetStaff = staffs.find((s) => s.staffId === staffId);

  if (targetStaff) {
    return getStaffName(targetStaff);
  }

  return '';
};

const StaffSelect: VFC<Props> = ({
  defaultStaffId = 0,
  label = '',
  onChange,
  error,
  helperText,
  disabled = false,
}) => {
  const [staffs, setStaffs] = useState<Staff[]>([]);
  const [staffNames, setStaffNames] = useState<string[]>([]);
  const auth = useAuthUser();

  useEffect(() => {
    const loadStaffs = async (): Promise<void> => {
      try {
        const staffsResult = await ky.get(`staffs`).json<StaffResult>();

        setStaffs(staffsResult.staffs);
        const staffNameList = staffsResult.staffs.map((s) => getStaffName(s));
        setStaffNames(staffNameList);
      } catch (err) {
        // eslint-disable-next-line no-console
        console.log('error');
      } finally {
        // setIsLoading(false);
      }
    };

    void loadStaffs();
  }, []);

  const staffName = getStaffNameById(staffs, defaultStaffId);

  const handleClickIcon = () => {
    if (auth) {
      const targetStaff = staffs.find(
        (s) => String(s.staffId) === auth.staffId,
      );

      if (targetStaff && onChange) {
        onChange(targetStaff);
      }
    }
  };

  const handleOnChange = (input: string | null): void => {
    const targetStaff = staffs.find((s) => input === getStaffName(s));
    if (onChange) {
      onChange(targetStaff);
    }
  };

  return (
    <StaffSelectPresenter
      staffNames={staffNames}
      handleClickIcon={handleClickIcon}
      handleOnChange={handleOnChange}
      staffName={staffName}
      label={label}
      error={error}
      helperText={helperText}
      disabled={disabled}
    />
  );
};

export default StaffSelect;
