import { VFC } from 'react';
import MultipleSelect from '../../molecules/MultipleSelect/MultipleSelect';

type NameValueItem = {
  name: string;
  value: string;
};

type Props = {
  items: NameValueItem[];
  label?: string;
  sideEffectIds?: string[];
  onChange?: (values: NameValueItem[]) => void;
};

const SideEffectSelectPresenter: VFC<Props> = ({
  items,
  label,
  sideEffectIds,
  onChange,
}) => (
  <MultipleSelect
    items={items}
    label={label}
    values={sideEffectIds}
    onChange={onChange}
  />
);

export default SideEffectSelectPresenter;
