import { FC } from 'react';
import { Box, Grid, InputAdornment, TextField } from '@mui/material';
import { AccountCircle } from '@mui/icons-material';
import AlertDialog from '@/components/molecules/ErrorDialog/AlertDialog';
import Header from '@/components/oraganisms/Header/Header';
import SideBar from '@/components/oraganisms/SideBar/SideBar';
import BarcodeReader from '@/components/oraganisms/BarcodeReader/BarcodeReader';

type Props = {
  drawerOpen: boolean;
  toggleDrawer: () => void;
  onLogout: () => void;
  patientId: string;
  orderId: string;
  onScan: (code: string) => void;
  dialogOpen: boolean;
  setDialogOpen: (dialogOpen: boolean) => void;
  transit: () => void;
};

const InjectionCheckPresenter: FC<Props> = ({
  drawerOpen,
  patientId,
  orderId,
  dialogOpen,
  toggleDrawer,
  onLogout,
  onScan,
  setDialogOpen,
  transit,
}) => (
  <>
    <Header onDrawerToggle={toggleDrawer} />
    <SideBar
      drawerOpen={drawerOpen}
      toggleDrawer={toggleDrawer}
      onOpen={onLogout}
    />
    <AlertDialog
      alertOpen={dialogOpen}
      message={`患者ID：${patientId}
      オーダID：${orderId}`}
      toggleAlert={() => {
        setDialogOpen(false);
      }}
      onEmpty={() => {
        setDialogOpen(false);
        transit();
      }}
    />
    <Box
      paddingTop="25px"
      sx={{
        flexGrow: 1,
      }}
    >
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <TextField
            id="patient-id"
            label="患者ID"
            color="success"
            value={patientId}
            focused={!!patientId}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AccountCircle />
                </InputAdornment>
              ),
            }}
            fullWidth
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            id="order-id"
            label="オーダID"
            color="success"
            value={orderId}
            focused={!!orderId}
            fullWidth
          />
        </Grid>
        <Grid item xs={12}>
          <BarcodeReader onScan={onScan} />
        </Grid>
      </Grid>
    </Box>
  </>
);

export default InjectionCheckPresenter;
