import { VFC, useState } from 'react';
import {
  Fab,
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Select,
  Stack,
  TextField,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import NavigationIcon from '@mui/icons-material/Navigation';
import { SubmitHandler, useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { format } from 'date-fns';
import { useLogout } from '@/contexts/auth/AuthUserContext';
import Header from '@/components/oraganisms/Header/Header';
import SideBar from '@/components/oraganisms/SideBar/SideBar';
import TransfusionPatientCard from '@/components/oraganisms/TransfusionPatientCard/TransfusionPatientCard';
import TransfusionOrder from '@/types/order/transfusion/transfusionOrder';
import crateStatusSummary from './orderStatusUtil';
import StaffSelect from '@/components/oraganisms/StaffSelect/StaffSelect';
import TransfusionDeliveryFormulation from '@/types/order/transfusion/transfusionDeliveryFormulation';
import TransfusionExecuteTypeSelect from '@/components/oraganisms/TransfusionExecuteTypeSelect/TransfusionExecuteTypeSelect';
import SideEffectSelect from '@/components/oraganisms/SideEffectSelect/SideEffectSelect';
import TransfusionExecute from '@/components/oraganisms/TransfusionPatientCard/TransfusionPatientCardTypes';

type Props = {
  transfusionOrder: TransfusionOrder;
  deliveryFormulation: TransfusionDeliveryFormulation;
  handleExecute: (input: TransfusionExecuteForm) => void;
};

export type TransfusionExecuteForm = {
  executeDate: string;
  executeStaffId: number;
  executeType: number;
  filterType: number;
  sideEffectIds: number[];
};

const toTransfusionExecute = (
  formulation: TransfusionDeliveryFormulation,
  targetFormulationNo: string,
  targetFormulationCode: string,
): TransfusionExecute => {
  const { formulationCode, formulationNo, formulationName } = formulation;
  const isExecuteTarget =
    formulationCode === targetFormulationCode &&
    formulationNo === targetFormulationNo;

  const { execute } = formulation;

  if (!execute) {
    return {
      isExecuteTarget,
      formulationName,
      formulationNo,
      executeDate: '',
      executeStaffName: '',
      executeTypeName: '',
      filterTypeName: '',
      sideEffectNames: '',
    };
  }

  const {
    executeDate,
    executeStaffName,
    executeTypeName,
    filterTypeName,
    sideEffects,
  } = execute;

  const sideEffectNames = sideEffects.map((s) => s.sideEffectName).join(',');

  return {
    isExecuteTarget,
    formulationName,
    formulationNo,
    executeDate,
    executeStaffName,
    executeTypeName,
    filterTypeName,
    sideEffectNames,
  };
};

const schema = yup.object({
  executeDate: yup.string().required('実施日時を入力してください。'),
  executeStaffId: yup.number().required('実施者を入力してください。'),
  executeType: yup.string().required('実施区分を入力してください。'),
  filterType: yup.string().required('フィルタ区分を入力してください。'),
});

const FabButton = styled(Fab)({
  position: 'fixed',
  right: 15,
  bottom: 15,
});

const TranfusionExecutePresenter: VFC<Props> = ({
  transfusionOrder,
  deliveryFormulation,
  handleExecute,
}) => {
  const { formulationName, formulationNo, formulationCode } =
    deliveryFormulation;

  const [drawerOpen, setDrawerOpen] = useState<boolean>(false);
  const logout = useLogout();
  const toggleDrawer = () => setDrawerOpen(!drawerOpen);
  const onLogout = () => logout();

  const status = crateStatusSummary(transfusionOrder);

  const { deliveryFormulations } = transfusionOrder.content;
  const executes = deliveryFormulations.map((f) =>
    toTransfusionExecute(f, formulationNo, formulationCode),
  );

  const targetExecute = deliveryFormulation.execute;

  const {
    register,
    handleSubmit,
    control,
    formState: { errors },
  } = useForm<TransfusionExecuteForm>({
    resolver: yupResolver(schema),
    defaultValues: {
      executeDate: targetExecute ? targetExecute.executeDate : '',
      executeStaffId: targetExecute ? targetExecute.executeStaffId : 0,
      executeType: targetExecute ? targetExecute.executeType : 1,
      filterType: targetExecute ? targetExecute.filterType : 1,
      sideEffectIds: targetExecute
        ? targetExecute.sideEffects.map((s) => s.sideEffectMstId)
        : [],
    },
  });

  const onSubmit: SubmitHandler<TransfusionExecuteForm> = (inputForm) => {
    const {
      executeDate,
      executeStaffId,
      executeType,
      filterType,
      sideEffectIds,
    } = inputForm;

    // if (inputForm) {
    //   console.log('return');
    //   alert('OK');

    //   return;
    // }

    const input: TransfusionExecuteForm = {
      executeDate,
      executeStaffId,
      executeType,
      filterType,
      sideEffectIds,
    };

    void handleExecute(input);
  };

  return (
    <>
      <Header onDrawerToggle={toggleDrawer} />
      <SideBar
        drawerOpen={drawerOpen}
        toggleDrawer={toggleDrawer}
        onOpen={onLogout}
      />
      <Stack
        component="form"
        noValidate
        spacing={3}
        paddingTop="25px"
        sx={{
          flexGrow: 1,
        }}
      >
        <TransfusionPatientCard
          patientId={transfusionOrder.patientId}
          summary={transfusionOrder.summary}
          status={status}
          executes={executes}
        />
        <TextField
          disabled
          id="formuration-name"
          label="製剤名"
          variant="filled"
          defaultValue={formulationName}
          InputProps={{
            readOnly: true,
          }}
        />
        <TextField
          disabled
          id="formuration-number"
          label="製剤番号"
          variant="filled"
          defaultValue={formulationNo}
          InputProps={{
            readOnly: true,
          }}
        />
        <TextField
          id="exec-date"
          label="使用日時"
          type="datetime-local"
          defaultValue={format(new Date(), "yyyy-MM-dd'T'HH:mm")}
          sx={{ width: 250 }}
          InputLabelProps={{
            shrink: true,
          }}
          error={'executeDate' in errors}
          helperText={errors.executeDate?.message}
          {...register('executeDate')}
        />
        <Controller
          control={control}
          name="executeStaffId"
          render={({ field: { onChange, value } }) => (
            <StaffSelect
              label="実施者"
              defaultStaffId={value}
              onChange={(s) => onChange(s?.staffId)}
              error={'executeStaffId' in errors}
              helperText={errors.executeStaffId?.message}
            />
          )}
        />
        <Controller
          control={control}
          name="executeType"
          render={({ field: { onChange, value } }) => (
            <TransfusionExecuteTypeSelect
              label="実施区分"
              displayExecuteTypes={['EXECUTE', 'RETURN']}
              onChange={(input) => onChange(input?.value)}
              value={value}
              error={'executeType' in errors}
              helperText={errors.executeType?.message}
            />
          )}
        />
        <FormControl error={'filterType' in errors}>
          <InputLabel id="filter-use-flg-label">フィルタ</InputLabel>
          <Select
            labelId="filter-use-flg-label"
            id="filter-use-flg"
            label="フィルタ"
            defaultValue={1}
            sx={{ width: 250 }}
            {...register('filterType')}
          >
            <MenuItem value={1}>使用</MenuItem>
            <MenuItem value={2}>未使用</MenuItem>
          </Select>
          <FormHelperText>{errors.filterType?.message}</FormHelperText>
        </FormControl>
        <Controller
          control={control}
          name="sideEffectIds"
          render={({ field: { value, onChange } }) => (
            <SideEffectSelect
              label="副作用"
              sideEffectIds={value}
              onChange={(sideEffects) => {
                onChange(sideEffects.map((s) => s.sideEffectMstId));
              }}
            />
          )}
        />
        <FabButton
          variant="extended"
          color="primary"
          aria-label="add-button"
          onClick={handleSubmit(onSubmit)}
        >
          <NavigationIcon sx={{ mr: 1 }} />
          実施入力
        </FabButton>
      </Stack>
    </>
  );
};

export default TranfusionExecutePresenter;
