import { VFC } from 'react';
import { format } from 'date-fns';
import {
  Card,
  CardContent,
  CardActions,
  CardHeader,
  IconButton,
  Typography,
  CircularProgress,
  Collapse,
} from '@mui/material';
import { AccountCircle } from '@mui/icons-material';
import Patient from '../../../types/patient';
import { getAge } from '../../../utils/age';

type Props = {
  patient?: Patient;
  cardActions?: JSX.Element;
  cardContent?: JSX.Element;
  displayContent?: boolean;
};

const PatientCardPresenter: VFC<Props> = ({
  patient,
  cardActions,
  cardContent,
  displayContent = false,
}) => {
  const birthDate = patient ? new Date(patient.birthday) : new Date();
  const formatBirthDate = patient ? format(birthDate, 'yyyy/MM/dd') : '';
  const age = patient ? `(${getAge(birthDate).toString()}歳)` : '';

  return patient ? (
    <>
      <Card variant="outlined">
        <CardHeader
          avatar={
            <IconButton aria-label="account">
              <AccountCircle fontSize="large" />
            </IconButton>
          }
          title={
            <>
              <Typography variant="h5" component="div">
                {`${patient.lastName} ${patient.firstName}`}
              </Typography>
              <Typography gutterBottom variant="body1" component="div">
                {`ID:${patient.patientId}`}
              </Typography>
              <Typography gutterBottom variant="body1" component="div">
                {`${formatBirthDate} ${age} ${patient.sex} ${patient.bloodType}型 ${patient.rhFactor}`}
              </Typography>
            </>
          }
        />
        {cardActions && <CardActions>{cardActions}</CardActions>}
        {cardContent && (
          <Collapse in={displayContent} timeout="auto" unmountOnExit>
            <CardContent>{cardContent}</CardContent>
          </Collapse>
        )}
      </Card>
    </>
  ) : (
    <Card variant="outlined">
      <CardHeader
        title={
          <>
            <CircularProgress sx={{ margin: 'auto' }} />
          </>
        }
      />
    </Card>
  );
};

export default PatientCardPresenter;
