import { FC } from 'react';
import { List, ListSubheader } from '@mui/material';
import NestListTree from '../NestList/NestListTree';
import type { TreeItem } from '../NestList/NestListTree';

type Props = {
  listDatas: TreeItem[];
  nestListItemClickAction: (injectionRouteName: TreeItem) => void;
};

const InjectionRouteNestList: FC<Props> = ({
  nestListItemClickAction,
  listDatas,
}) => {
  // ネストリストの末端の項目を選択した時のアクション
  const nestListItemClick = (treeItem: TreeItem) => {
    nestListItemClickAction(treeItem);
  };

  return (
    <>
      <List
        sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}
        component="nav"
        aria-labelledby="nested-list-subheader"
        subheader={
          <ListSubheader component="div" id="nested-list-subheader">
            投与ルート選択
          </ListSubheader>
        }
      >
        <NestListTree
          treeItems={listDatas}
          nestListItemClickAction={nestListItemClick}
        />
      </List>
    </>
  );
};

export default InjectionRouteNestList;
