import React, { FC, useEffect, useState } from 'react';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import { Autocomplete } from '@mui/lab';
import {
  Fab,
  Dialog,
  InputAdornment,
  Backdrop,
  Snackbar,
  Alert,
} from '@mui/material';
import { isAfter } from 'date-fns';
import NavigationIcon from '@mui/icons-material/Navigation';
import ky from 'ky';
import { styled } from '@mui/material/styles';
import { useNavigate } from 'react-router';
import InjectionRouteNestList from '../../../oraganisms/InjectionRouteNestList/InjectionRouteNestList';
import type { TreeItem } from '../../../oraganisms/NestList/NestListTree';
import doExecution from '../InjectionExecution';
import type { InjectionExecutePatch } from '../InjectionExecution';
import Staff from '../../../../types/staff';
import StaffSelect from '../../../oraganisms/StaffSelect/StaffSelect';
import { useAuthUser } from '../../../../contexts/auth/AuthUserContext';
import { InjectionExecute } from '@/types/order/injection/injectionExecute';
import { InjectionOrder } from '@/types/order/injection/injectionOrder';
import InjectionPatientCard from '../../../oraganisms/InjectionPatientCard/InjectionPatientCard';

type CommentData = {
  executeCommentId: string;
  content: string;
  displayIndex: string;
  startDate: string;
  endDate: string;
};

type Result = {
  executeCommentItems: CommentData[];
  routeNestDatas: TreeItem[];
};

type Props = {
  injectionOrder: InjectionOrder;
};

const FabButton = styled(Fab)({
  position: 'fixed',
  right: 15,
  bottom: 15,
});

const OneShotExec: FC<Props> = ({ injectionOrder }) => {
  const navigate = useNavigate();
  const auth = useAuthUser();

  const editable: boolean = injectionOrder?.orderStatus !== 4;

  const [successBarOpen, setSuccessBarOpen] = useState<boolean>(false);
  const [errorBarOpen, setErrorBarOpen] = useState<boolean>(false);

  const [oneShotRecord, _] = useState<InjectionExecute>(
    injectionOrder.execList.length !== 0
      ? injectionOrder.execList[0]
      : {
          injectionExecutionId: 0,
          executionStartId: 0,
        },
  );
  // 実施コメント
  const [executeCommentDatas, setExecuteCommentDatas] = useState<CommentData[]>(
    [],
  );

  const [executeCommentId, setExecuteCommentId] = useState<number | undefined>(
    undefined,
  );

  const [executeCommentValue, setExecuteCommentValue] = useState<
    string | undefined
  >(undefined);

  // 投与ルートネストリストダイアログ
  const [openInjectionRouteDialog, setOpenInjectionRouteDialog] =
    useState(false);

  const [nestListDatas, setNestListDatas] = useState<TreeItem[]>([]);

  const injectionRouteNestListOpen = () => {
    setOpenInjectionRouteDialog(!openInjectionRouteDialog);
  };

  const [injectionRouteId, setInjectionRouteId] = useState<number | undefined>(
    undefined,
  );

  // 実施開始日
  const [startDate, setStartDate] = useState<string | undefined>(undefined);

  const [
    injectionRouteNameTextFieldValue,
    setInjectionRouteNameTextFieldValue,
  ] = useState<string | undefined>(undefined);

  // 投与ルート
  const onClickInjectionRoute = () => {
    injectionRouteNestListOpen();
  };

  const setInjectionRoute = (injectionRouteData: TreeItem): void => {
    setInjectionRouteId(injectionRouteData.itemId);
    setInjectionRouteNameTextFieldValue(injectionRouteData.value);
    injectionRouteNestListOpen();
  };

  const setupStartStaffId = (): number => {
    if (
      oneShotRecord.executionStartId !== undefined &&
      oneShotRecord.executionStartId !== 0
    ) {
      return oneShotRecord.executionStartId;
    }
    if (auth) {
      return Number(auth.staffId);
    }

    return 0;
  };

  // 実施開始者
  const [startStaff, setStartStaff] = useState<Staff>();

  // 投与量
  const [amount, setAmount] = useState<number | undefined>(undefined);

  const onChangeAmount = (event: React.ChangeEvent<HTMLInputElement>) => {
    setAmount(Number(event.target.value));
  };

  // エラー状態管理用のstate

  const [injectionRouteError, setInjectionRouteError] = useState(false);

  const [injectionRouteErrorHelperText, setInjectionRouteErrorHelperText] =
    useState('');

  const [startDateError, setStartDateError] = useState(false);

  const [startDateErrorHelperText, setStartDateErrorHelperText] = useState('');

  const [startStaffError, setStartStaffError] = useState(false);

  const [startStaffErrorHelperText, setStartStaffErrorHelperText] =
    useState('');

  const [amountError, setAmountError] = useState(false);

  const [amountErrorHelperText, setAmountErrorHelperText] = useState('');

  // 入力チェック
  const inputCheck = (): boolean => {
    const isInjectionRouteInputError = injectionRouteId === 0;

    // 投与ルートの入力チェック
    setInjectionRouteError(isInjectionRouteInputError);
    setInjectionRouteErrorHelperText(
      isInjectionRouteInputError ? '投与ルートを指定してください。' : '',
    );

    // 実施開始日入力チェック
    let isStartDateError: boolean;
    if (startDate == null) {
      isStartDateError = true;
      setStartDateError(isStartDateError);
      setStartDateErrorHelperText('実施開始日時を指定してください。');
    } else if (isAfter(new Date(startDate), new Date())) {
      isStartDateError = true;
      setStartDateError(isStartDateError);
      setStartDateErrorHelperText('実施開始日時に未来日は指定できません。');
    } else {
      isStartDateError = false;
      setStartDateError(isStartDateError);
      setStartDateErrorHelperText('');
    }

    // 実施開始者入力チェック
    const isStartStaffInputError = startStaff?.staffId === 0;
    setStartStaffError(isStartStaffInputError);
    setStartStaffErrorHelperText(
      isStartStaffInputError ? '開始者を指定してください。' : '',
    );

    // 投与量入力チェック
    const isAmountInputError = typeof amount === 'number' && amount > 9999.999;
    setAmountError(isAmountInputError);
    setAmountErrorHelperText(
      isAmountInputError
        ? '投与量は、整数4ケタ、小数3ケタ以内で入力してください。'
        : '',
    );

    const isError = (): boolean =>
      isInjectionRouteInputError ||
      isStartDateError ||
      isStartStaffInputError ||
      isAmountInputError;

    if (isError()) {
      return false;
    }

    return true;
  };

  useEffect(() => {
    const load = async (): Promise<void> => {
      try {
        const result = await ky
          .get(
            'http://localhost:8080/jerseywebapp/rest/order/injection/execute/oneshot',
            {
              searchParams: {
                reserveDate: injectionOrder.reserveDate,
              },
            },
          )
          .json<Result>();

        setExecuteCommentDatas(result.executeCommentItems);
        setNestListDatas(result.routeNestDatas);
      } catch (err) {
        throw new Error('error');
      }
    };

    void load();
  }, [injectionOrder.reserveDate]);

  useEffect(() => {
    setExecuteCommentId(oneShotRecord.executionCommentId);
    setExecuteCommentValue(oneShotRecord.executionFreeComment);
    setInjectionRouteId(injectionOrder?.injectionRouteId);
    setInjectionRouteNameTextFieldValue(injectionOrder?.injectionRouteName);
    setStartDate(oneShotRecord.executionStartDate);
    setAmount(
      injectionOrder?.orderStatus === 4
        ? oneShotRecord.executionAmount
        : injectionOrder?.totalAmount,
    );
  }, [injectionOrder, oneShotRecord]);

  const executeButtonAction = async () => {
    if (injectionOrder?.orderStatus !== 4) {
      if (!inputCheck()) {
        return;
      }
    }

    const itemD: InjectionExecute = {
      injectionRouteMstId: injectionRouteId,
      executionStartDate: startDate,
      executionStartId: startStaff?.staffId,
      executionCommentId: executeCommentId,
      executionFreeComment: executeCommentValue,
      executionAmount: amount,
    };

    const executeData: InjectionExecutePatch = {
      orderHeaderId: injectionOrder?.orderHeaderId ?? 0,
      version: injectionOrder?.version ?? 0,
      updateId: auth ? Number(auth.staffId) : 0,
      end: true,
      execList: injectionOrder?.orderStatus !== 4 ? [itemD] : [],
    };

    const result = await doExecution(executeData);

    if (result) {
      setSuccessBarOpen(true);
    } else {
      setErrorBarOpen(true);
    }
  };

  return (
    <>
      <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={successBarOpen}
      />
      <Snackbar
        open={successBarOpen}
        autoHideDuration={6000}
        onClose={() => {
          setSuccessBarOpen(false);
          navigate(-1);
        }}
      >
        <Alert
          onClose={() => {
            setSuccessBarOpen(false);
            navigate(-1);
          }}
          severity="success"
          sx={{ width: '100%' }}
        >
          {injectionOrder?.orderStatus !== 4
            ? '実施情報を登録しました。'
            : '実施取消を行いました。'}
        </Alert>
      </Snackbar>

      <Snackbar
        open={errorBarOpen}
        autoHideDuration={6000}
        onClose={() => {
          setErrorBarOpen(false);
        }}
      >
        <Alert
          onClose={() => {
            setErrorBarOpen(false);
          }}
          severity="error"
          sx={{ width: '100%' }}
        >
          実施情報の更新に失敗しました。
        </Alert>
      </Snackbar>
      <FabButton
        variant="extended"
        color="primary"
        aria-label="add-button"
        onClick={executeButtonAction}
      >
        <NavigationIcon sx={{ mr: 1 }} />
        {injectionOrder?.execList.length === 0 ? '確定' : '実施取消'}
      </FabButton>

      <InjectionPatientCard
        patientId={injectionOrder.patientId}
        injectionOrder={injectionOrder}
        executes={injectionOrder.execList}
      />

      <Grid container spacing={2} direction="column">
        <Grid item>
          <TextField
            defaultValue=" "
            disabled={!editable}
            error={injectionRouteError}
            helperText={injectionRouteErrorHelperText}
            id="injectionRouteTextField"
            label="投与ルート"
            variant="filled"
            value={injectionRouteNameTextFieldValue}
            onClick={onClickInjectionRoute}
            InputProps={{
              readOnly: true,
            }}
          />
          <Dialog
            open={openInjectionRouteDialog}
            onClose={injectionRouteNestListOpen}
          >
            <InjectionRouteNestList
              nestListItemClickAction={setInjectionRoute}
              listDatas={nestListDatas}
            />
          </Dialog>
        </Grid>
        <Grid item>
          <TextField
            label="実施開始日時"
            type="datetime-local"
            error={startDateError}
            disabled={!editable}
            helperText={startDateErrorHelperText}
            value={startDate}
            sx={{ width: 250 }}
            InputLabelProps={{
              shrink: true,
            }}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              setStartDate(event.target.value);
            }}
          />
        </Grid>
        <Grid item>
          <StaffSelect
            label="開始者"
            defaultStaffId={setupStartStaffId()}
            error={startStaffError}
            helperText={startStaffErrorHelperText}
            onChange={(staff?: Staff) => {
              if (staff !== undefined) {
                setStartStaff(staff);
              }
            }}
          />
        </Grid>
        <Grid item>
          <TextField
            error={amountError}
            helperText={amountErrorHelperText}
            id="amountTextField"
            label="投与量"
            variant="outlined"
            type="number"
            disabled={!editable}
            value={amount}
            onChange={onChangeAmount}
            InputProps={{
              endAdornment: <InputAdornment position="end">ml</InputAdornment>,
            }}
          />
        </Grid>
        <Grid item>
          <Autocomplete
            freeSolo
            disabled={!editable}
            onChange={(event, value) => {
              if (typeof value === 'string') {
                setExecuteCommentId(Number(0));
                setExecuteCommentValue(value);
              } else if (value != null) {
                setExecuteCommentId(Number(value?.executeCommentId));
                setExecuteCommentValue(value?.content);
              }
            }}
            value={
              executeCommentId !== 0
                ? executeCommentDatas.find(
                    (e) => Number(e.executeCommentId) === executeCommentId,
                  )
                : executeCommentValue
            }
            options={executeCommentDatas}
            getOptionLabel={(option) =>
              typeof option === 'string' ? option : option.content
            }
            renderInput={(params) => (
              <TextField {...params} label="実施コメント" />
            )}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default OneShotExec;
