import { VFC } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import { styled } from '@mui/material/styles';
import { Typography } from '@mui/material';

type Props = {
  message: string;
  alertOpen: boolean;
  toggleAlert: () => void;
  onEmpty: () => void;
};

const Alert = styled(Dialog)(() => ({
  fontFamily: '-apple-system, BlinkMacSystemFont, Roboto, sans-serif',
}));

const AlertDialog: VFC<Props> = ({
  message = '本当にごみ箱を完全に空にしますか？',
  alertOpen,
  toggleAlert,
  onEmpty,
}) => (
  <Alert open={alertOpen} onClose={toggleAlert}>
    <DialogTitle>バーコード読取</DialogTitle>
    <DialogContent>
      <DialogContentText>
        {message
          .split(/(\n)/)
          .filter((item) => /\n/.exec(item) === null)
          .map((item) => (
            <Typography>{item}</Typography>
          ))}
      </DialogContentText>
    </DialogContent>
    <DialogActions>
      <Button onClick={toggleAlert} color="primary" aria-label="cancel">
        キャンセル
      </Button>
      <Button
        onClick={() => {
          toggleAlert();
          onEmpty();
        }}
        color="secondary"
        aria-label="ok"
        autoFocus
      >
        OK
      </Button>
    </DialogActions>
  </Alert>
);

export default AlertDialog;
