import { FC, useEffect, useState } from 'react';
import {
  TextField,
  Button,
  Grid,
  InputAdornment,
  Dialog,
  Autocomplete,
  Stack,
} from '@mui/material';
import { isAfter, isBefore } from 'date-fns';
import ky from 'ky';
import InjectionRouteNestList from '../../../oraganisms/InjectionRouteNestList/InjectionRouteNestList';
import type { TreeItem } from '../../../oraganisms/NestList/NestListTree';
import Staff from '../../../../types/staff';
import StaffSelect from '../../../oraganisms/StaffSelect/StaffSelect';
import { useAuthUser } from '../../../../contexts/auth/AuthUserContext';
import { InjectionExecute } from '@/types/order/injection/injectionExecute';

type Prop = {
  defaultExecute: InjectionExecute;
  onInput: (input: InjectionExecute, isEndFlg: boolean) => void;
  executes: InjectionExecute[];
  isShowRemainAmount: boolean;
  totalAmount: number;
  reserveDate: string;
};

type Comment = {
  commentId: string;
  content: string;
  displayIndex: string;
  startDate: string;
  endDate: string;
};

type Result = {
  executeCommentItems: Comment[];
  endCommentItems: Comment[];
  routeNestDatas: TreeItem[];
};

const DropExecuteItemInput: FC<Prop> = ({
  defaultExecute,
  onInput,
  executes,
  isShowRemainAmount,
  totalAmount,
  reserveDate,
}) => {
  const [dropRecordId, setDropRecordId] = useState(
    defaultExecute.injectionExecutionId,
  );
  const auth = useAuthUser();

  // 投与ルートネストリストダイアログ
  const [openInjectionRouteDialog, setOpenInjectionRouteDialog] =
    useState(false);

  const injectionRouteNestListOpen = () => {
    setOpenInjectionRouteDialog(!openInjectionRouteDialog);
  };

  const [injectionRouteId, setInjectionRouteId] = useState(
    defaultExecute.injectionRouteMstId,
  );

  const [nestListDatas, setNestListDatas] = useState<TreeItem[]>([]);

  // 投与ルート
  const onClickInjectionRoute = () => {
    injectionRouteNestListOpen();
  };

  const [injectionRouteName, setInjectionRouteName] = useState<
    string | undefined
  >(undefined);

  const setInjectionRoute = (injectionRouteData: TreeItem): void => {
    setInjectionRouteId(injectionRouteData.itemId);
    setInjectionRouteName(injectionRouteData.value);
    injectionRouteNestListOpen();
  };

  // 点滴速度
  const [dropSpeed, setDropSpeed] = useState<number | undefined>(
    defaultExecute.executionDropSpeed,
  );

  const onChangeDropSpeed = (event: React.ChangeEvent<HTMLInputElement>) => {
    setDropSpeed(
      Number(event.target.value) < 0 ? 0 : Number(event.target.value),
    );
  };

  // 実施開始日
  const [startDate, setStartDate] = useState(defaultExecute.executionStartDate);

  const setupStartStaffId = (): number => {
    if (
      defaultExecute.executionStartId !== undefined &&
      defaultExecute.executionStartId !== 0
    ) {
      return defaultExecute.executionStartId;
    }
    if (auth) {
      return Number(auth.staffId);
    }

    return 0;
  };

  // 実施開始者
  const [startStaff, setStartStaff] = useState<Staff>();

  // 実施終了日
  const [endDate, setEndDate] = useState(defaultExecute.executionEndDate);

  // 実施終了者
  const [endStaff, setEndStaff] = useState<Staff>();

  // 実施コメント

  const [executeCommentId, setExecuteCommentId] = useState<number | undefined>(
    defaultExecute.executionCommentId,
  );

  const [executeCommentValue, setExecuteCommentValue] = useState<
    string | undefined
  >(defaultExecute.executionFreeComment);

  // 投与量
  const [amount, setAmount] = useState<number | undefined>(
    defaultExecute.executionAmount,
  );

  const onChangeAmount = (event: React.ChangeEvent<HTMLInputElement>) => {
    setAmount(Number(event.target.value));
  };

  // 残
  const [remainAmount, setRemainAmount] = useState<number | undefined>(0);

  const onChangeRemainAmount = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRemainAmount(Number(event.target.value));
  };

  // 終了コメント
  const [endCommentId, setEndCommentId] = useState<number | undefined>(
    defaultExecute.endCommentId,
  );

  const [endCommentValue, setEndCommentValue] = useState<string | undefined>(
    defaultExecute.endFreeComment,
  );

  const [endCommentDatas, setEndCommentDatas] = useState<Comment[]>([]);

  const [executeCommentDatas, setExecuteCommentDatas] = useState<Comment[]>([]);

  // エラー状態管理用のstate

  const [injectionRouteError, setInjectionRouteError] = useState(false);

  const [injectionRouteErrorHelperText, setInjectionRouteErrorHelperText] =
    useState('');

  const [dropSpeedError, setDropSpeedError] = useState(false);

  const [dropSpeedErrorHelperText, setDropSpeedErrorHelperText] = useState('');

  const [startDateError, setStartDateError] = useState(false);

  const [startDateErrorHelperText, setStartDateErrorHelperText] = useState('');

  const [startStaffError, setStartStaffError] = useState(false);

  const [startStaffErrorHelperText, setStartStaffErrorHelperText] =
    useState('');

  const [endStaffError, setEndStaffError] = useState(false);

  const [endStaffErrorHelperText, setEndStaffErrorHelperText] = useState('');

  const [amountError, setAmountError] = useState(false);

  const [amountErrorHelperText, setAmountErrorHelperText] = useState('');

  const [endDateError, setEndDateError] = useState(false);

  const [endDateErrorHelperText, setEndDateErrorHelperText] = useState('');

  const onClickDosagaCal = () => {
    // 点滴速度、開始・終了日時、投与量の入力チェック

    const isDropSpeedInputError: boolean =
      dropSpeed === undefined || dropSpeed === 0;

    setDropSpeedError(isDropSpeedInputError);
    setDropSpeedErrorHelperText(
      isDropSpeedInputError ? '点滴速度を入力してください。' : '',
    );

    const isStartDateInputError: boolean = startDate == null;

    setStartDateError(isStartDateInputError);
    setStartDateErrorHelperText(
      isStartDateInputError ? '実施開始日時を指定してください。' : '',
    );

    const isEndDateInputError: boolean = endDate == null;

    setEndDateError(isEndDateInputError);
    setEndDateErrorHelperText(
      isEndDateInputError ? '実施終了日時を指定してください。' : '',
    );

    const isEndDateFutureDateError =
      startDate != null &&
      endDate != null &&
      isAfter(new Date(startDate), new Date(endDate));

    if (endDate != null) {
      setEndDateError(isEndDateFutureDateError);
      setEndDateErrorHelperText(
        isEndDateFutureDateError
          ? '実施終了日時には実施開始日時以降を指定してください。'
          : '',
      );
    }

    if (
      !isDropSpeedInputError &&
      !isStartDateInputError &&
      !isEndDateInputError &&
      !isEndDateFutureDateError &&
      typeof dropSpeed === 'number' &&
      typeof startDate === 'string' &&
      typeof endDate === 'string'
    ) {
      // 差分を秒で取得
      const second: number =
        new Date(startDate).getTime() === new Date(endDate).getTime()
          ? 0
          : (new Date(endDate).getTime() - new Date(startDate).getTime()) /
            1000;

      // 時間に変換
      const mintes: number = second / 60;
      const time: number = mintes / 60;

      const resultAmount = dropSpeed * time;

      setAmount(Number(resultAmount.toFixed(3)));
    }
  };

  const isImiidateEnd = (): boolean =>
    executes.length === 0 ||
    (executes.length === 1 &&
      executes.some((e) => e.injectionExecutionId === dropRecordId));

  // 入力チェック
  const inputCheck = (isEndAdd: boolean): boolean => {
    // 追加済みの実施がある場合、追加するには前の実施が終了している必要あり
    let lastRow: InjectionExecute = {};

    let isNotFinishError: boolean;

    if (executes.length > 0) {
      let lastRowIndex = -1;
      if (dropRecordId === 0) {
        // 新規の場合は最終行を対象とする
        lastRowIndex = executes.length - 1;
      } else if (
        executes.some((e) => e.injectionExecutionId === dropRecordId)
      ) {
        // 編集の場合は一つ前の行インデックスを取得(2行目以降)
        lastRowIndex =
          executes.findIndex((e) => e.injectionExecutionId === dropRecordId) -
          1;
      }

      if (lastRowIndex >= 0) {
        lastRow = executes[lastRowIndex];
        if (!lastRow.executionEndDate) {
          isNotFinishError = true;
          setEndDateError(isNotFinishError);
          setEndDateErrorHelperText('前の実施が終了されていません。');

          return false;
        }
        isNotFinishError = false;
        setEndDateError(isNotFinishError);
        setEndDateErrorHelperText('');
      }
    }

    const isInjectionRouteInputError = injectionRouteId === 0;

    // 投与ルートの入力チェック
    setInjectionRouteError(isInjectionRouteInputError);
    setInjectionRouteErrorHelperText(
      isInjectionRouteInputError ? '投与ルートを指定してください。' : '',
    );

    // 実施開始日入力チェック
    let isStartDateError: boolean;
    if (startDate == null) {
      isStartDateError = true;
      setStartDateError(isStartDateError);
      setStartDateErrorHelperText('実施開始日時を指定してください。');
    } else if (
      lastRow !== {} &&
      typeof lastRow.executionEndDate === 'string' &&
      isBefore(new Date(startDate), new Date(lastRow.executionEndDate))
    ) {
      isStartDateError = true;
      setStartDateError(isStartDateError);
      setStartDateErrorHelperText('実施期間が重複しています。');
    } else if (isAfter(new Date(startDate), new Date())) {
      isStartDateError = true;
      setStartDateError(isStartDateError);
      setStartDateErrorHelperText('実施開始日時に未来日は指定できません。');
    } else {
      isStartDateError = false;
      setStartDateError(isStartDateError);
      setStartDateErrorHelperText('');
    }

    // 実施開始者入力チェック
    const isStartStaffInputError = startStaff?.staffId === 0;
    setStartStaffError(isStartStaffInputError);
    setStartStaffErrorHelperText(
      isStartStaffInputError ? '開始者を指定してください。' : '',
    );

    // 投与量入力チェック
    const isAmountInputError = typeof amount === 'number' && amount > 9999.999;
    setAmountError(isAmountInputError);
    setAmountErrorHelperText(
      isAmountInputError
        ? '投与量は、整数4ケタ、小数3ケタ以内で入力してください。'
        : '',
    );

    let isEndDateError: boolean;
    let isEndStaffInputError: boolean;

    if (isEndAdd) {
      // -----------------------------------------------------------------
      // 終了追加の場合
      // -----------------------------------------------------------------
      // ⇒ 簡易的な入力を許容するため、即終了(*)の場合は
      // 終了日の必須チェックを無しとします。
      // ※終了日が省略された場合、終了日＝開始日 として保存されます。
      // この場合、速度行では、ワンショットと同じ形式で表示されます。
      //
      // (*) 即終了：施用が１つしかない状態で、終了追加された場合
      // -----------------------------------------------------------------

      if (endDate == null && !isImiidateEnd) {
        isEndDateError = true;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText('実施終了日時を指定してください。');
      } else if (
        endDate != null &&
        startDate != null &&
        isAfter(new Date(startDate), new Date(endDate))
      ) {
        isEndDateError = true;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText(
          '実施終了日時には実施開始日時以降を指定してください。',
        );
      } else if (endDate != null && isAfter(new Date(endDate), new Date())) {
        isEndDateError = true;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText('実施終了日時に未来日は指定できません。');
      } else {
        isEndDateError = false;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText('');
      }

      isEndStaffInputError = endStaff?.staffId === 0 && !!isImiidateEnd;
      setEndStaffError(isEndStaffInputError);
      setEndStaffErrorHelperText(
        isEndStaffInputError ? '実施終了者を指定してください。' : '',
      );
    } else {
      // --------------------------------------------
      // 追加の場合
      // --------------------------------------------
      // 実施終了日時・実施終了者の整合性チェック
      if (endDate == null) {
        isEndDateError = true;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText('実施終了日時を指定してください。');
      } else if (
        endDate != null &&
        startDate != null &&
        isAfter(new Date(startDate), new Date(endDate))
      ) {
        isEndDateError = true;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText(
          '実施終了日時には実施開始日時以降を指定してください。',
        );
      } else if (endDate != null && isAfter(new Date(endDate), new Date())) {
        isEndDateError = true;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText('実施終了日時に未来日は指定できません。');
      } else {
        isEndDateError = false;
        setEndDateError(isEndDateError);
        setEndDateErrorHelperText('');
      }

      isEndStaffInputError = endStaff?.staffId === 0;
      setEndStaffError(isEndStaffInputError);
      setEndStaffErrorHelperText(
        isEndStaffInputError ? '実施終了者を指定してください。' : '',
      );
    }

    const isError = (): boolean =>
      isNotFinishError ||
      isInjectionRouteInputError ||
      isStartDateError ||
      isStartStaffInputError ||
      isAmountInputError ||
      isEndDateError ||
      isEndStaffInputError;

    if (isError()) {
      return false;
    }

    return true;
  };

  // 追加ボタンを押したときのアクション
  const doAddButton = () => {
    if (!inputCheck(false)) {
      return;
    }
    const itemD: InjectionExecute = {
      injectionExecutionId: dropRecordId,
      executionStartDate: startDate,
      injectionRouteMstId: injectionRouteId,
      executionStartId: startStaff?.staffId,
      executionEndDate: endDate,
      executionEndId: endStaff?.staffId,
      executionCommentId: executeCommentId,
      executionFreeComment: executeCommentValue,
      endCommentId,
      endFreeComment: endCommentValue,
      executionAmount: amount,
      executionDropSpeed: dropSpeed,
    };
    onInput(itemD, false);
  };

  const doEndAddButton = () => {
    if (!inputCheck(true)) {
      return;
    }
    const itemD: InjectionExecute = {
      injectionExecutionId: dropRecordId,
      executionStartDate: startDate,
      injectionRouteMstId: injectionRouteId,
      executionStartId: startStaff?.staffId,
      executionEndDate: endDate,
      executionEndId: endStaff?.staffId,
      executionCommentId: executeCommentId,
      executionFreeComment: executeCommentValue,
      endCommentId,
      endFreeComment: endCommentValue,
      executionAmount: amount,
      executionDropSpeed: dropSpeed,
    };
    onInput(itemD, true);
  };

  useEffect(() => {
    const load = async (): Promise<void> => {
      try {
        const result = await ky
          .get(
            'http://localhost:8080/jerseywebapp/rest/order/injection/execute/drop',
            {
              searchParams: {
                reserveDate,
              },
            },
          )
          .json<Result>();

        setExecuteCommentDatas(result.executeCommentItems);
        setEndCommentDatas(result.endCommentItems);
        setNestListDatas(result.routeNestDatas);
      } catch (err) {
        throw new Error('error');
      }
    };

    void load();
  }, [reserveDate]);

  useEffect(() => {
    setDropRecordId(defaultExecute.injectionExecutionId);
    setInjectionRouteId(defaultExecute.injectionRouteMstId);
    setInjectionRouteName(
      nestListDatas.find((e) => e.itemId === defaultExecute.injectionRouteMstId)
        ?.value,
    );
    setDropSpeed(defaultExecute.executionDropSpeed);
    setStartDate(defaultExecute.executionStartDate);
    setEndDate(defaultExecute.executionEndDate);
    setExecuteCommentId(defaultExecute.executionCommentId);
    setExecuteCommentValue(defaultExecute.executionFreeComment);
    setAmount(defaultExecute.executionAmount);

    let amountSum = 0;

    executes.forEach((e) => {
      if (e.executionAmount) {
        amountSum += e.executionAmount;
      }
    });

    const remain: number = totalAmount - amountSum;

    setRemainAmount(remain >= 0 ? remain : 0);
  }, [nestListDatas, totalAmount, defaultExecute, executes]);

  return (
    <>
      <Stack
        component="form"
        noValidate
        spacing={3}
        paddingLeft="18px"
        sx={{
          flexGrow: 1,
        }}
      >
        <Grid container spacing={2} direction="column">
          <Grid item>
            <TextField
              defaultValue=" "
              error={injectionRouteError}
              helperText={injectionRouteErrorHelperText}
              id="injectionRouteTextField"
              label="投与ルート"
              variant="filled"
              value={injectionRouteName}
              onClick={onClickInjectionRoute}
              InputProps={{
                readOnly: true,
              }}
            />
            <Dialog
              open={openInjectionRouteDialog}
              onClose={injectionRouteNestListOpen}
            >
              <InjectionRouteNestList
                nestListItemClickAction={setInjectionRoute}
                listDatas={nestListDatas}
              />
            </Dialog>
          </Grid>
          <Grid item>
            <TextField
              error={dropSpeedError}
              className="dropSpeed"
              id="dropSpeedTextField"
              label="点滴速度"
              variant="outlined"
              type="number"
              value={dropSpeed}
              onChange={onChangeDropSpeed}
              helperText={dropSpeedErrorHelperText}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">ml/時間</InputAdornment>
                ),
              }}
            />
          </Grid>
          <Grid item>
            <TextField
              label="実施開始日時"
              type="datetime-local"
              error={startDateError}
              helperText={startDateErrorHelperText}
              value={startDate}
              sx={{ width: 250 }}
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                setStartDate(event.target.value);
              }}
            />
          </Grid>
          <Grid item>
            <StaffSelect
              label="開始者"
              defaultStaffId={setupStartStaffId()}
              error={startStaffError}
              helperText={startStaffErrorHelperText}
              onChange={(staff?: Staff) => {
                if (staff !== undefined) {
                  setStartStaff(staff);
                }
              }}
            />
          </Grid>
          <Grid item>
            <TextField
              label="実施終了日時"
              type="datetime-local"
              error={endDateError}
              helperText={endDateErrorHelperText}
              value={endDate}
              sx={{ width: 250 }}
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                setEndDate(event.target.value);
              }}
            />
          </Grid>
          <Grid item>
            <StaffSelect
              label="終了者"
              error={endStaffError}
              helperText={endStaffErrorHelperText}
              onChange={(staff?: Staff) => {
                if (staff !== undefined) {
                  setEndStaff(staff);
                }
              }}
            />
          </Grid>
          <Grid item>
            <TextField
              className="amount"
              error={amountError}
              helperText={amountErrorHelperText}
              id="amountTextField"
              label="投与量"
              variant="outlined"
              type="number"
              value={amount}
              onChange={onChangeAmount}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">ml</InputAdornment>
                ),
              }}
            />
            <Button variant="contained" onClick={onClickDosagaCal}>
              用量計算
            </Button>
          </Grid>
          {isShowRemainAmount && (
            <Grid item>
              <TextField
                className="remainAmount"
                id="remainAmountTextField"
                label="残量"
                variant="outlined"
                type="number"
                value={remainAmount}
                onChange={onChangeRemainAmount}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">ml</InputAdornment>
                  ),
                  readOnly: true,
                }}
              />
            </Grid>
          )}
          <Grid item>
            <Autocomplete
              freeSolo
              onChange={(event, value) => {
                if (typeof value === 'string') {
                  setExecuteCommentId(Number(0));
                  setExecuteCommentValue(value);
                } else if (value != null) {
                  setExecuteCommentId(Number(value?.commentId));
                  setExecuteCommentValue(value?.content);
                }
              }}
              value={
                executeCommentId !== 0
                  ? executeCommentDatas.find(
                      (e) => Number(e.commentId) === executeCommentId,
                    )
                  : executeCommentValue
              }
              options={executeCommentDatas}
              getOptionLabel={(option) =>
                typeof option === 'string' ? option : option.content
              }
              renderInput={(params) => (
                <TextField {...params} label="実施コメント" />
              )}
            />
          </Grid>
          <Grid item>
            <Autocomplete
              freeSolo
              onChange={(event, value) => {
                if (typeof value === 'string') {
                  setEndCommentId(Number(0));
                  setEndCommentValue(value);
                } else if (value != null) {
                  setEndCommentId(Number(value?.commentId));
                  setEndCommentValue(value?.content);
                }
              }}
              value={
                endCommentId !== 0
                  ? endCommentDatas.find(
                      (e) => Number(e.commentId) === endCommentId,
                    )
                  : endCommentValue
              }
              options={endCommentDatas}
              getOptionLabel={(option) =>
                typeof option === 'string' ? option : option.content
              }
              renderInput={(params) => (
                <TextField {...params} label="終了コメント" />
              )}
            />
          </Grid>
          <Grid item>
            <Button variant="contained" color="success" onClick={doAddButton}>
              追加
            </Button>
            <Button
              variant="contained"
              color="success"
              onClick={doEndAddButton}
            >
              終了追加
            </Button>
          </Grid>
        </Grid>
      </Stack>
    </>
  );
};

export default DropExecuteItemInput;
