import { FC, useState } from 'react';
import { List, Collapse } from '@mui/material';
import NestListNode from './NestListNode';
import type { TreeItem } from './NestListTree';

type Props = {
  item: TreeItem;
  level: number;
  nestListItemClickAction: (treeItem: TreeItem) => void;
};

const NestListBranch: FC<Props> = (props) => {
  const { item, level, nestListItemClickAction } = props;

  const isExistChildren =
    item.children !== null &&
    item.children !== undefined &&
    item.children.length !== 0;

  const [open, setOpen] = useState(false);

  const renderBranches = () => {
    if (isExistChildren) {
      const newLevel = level + 1;

      return item.children?.map((child) => (
        <Collapse in={open} timeout="auto" unmountOnExit>
          <List disablePadding>
            <NestListBranch
              key={child?.itemId}
              item={child}
              level={newLevel}
              nestListItemClickAction={nestListItemClickAction}
            />
          </List>
        </Collapse>
      ));
    }

    return null;
  };

  return (
    <>
      <NestListNode
        item={item}
        isExistChildren={isExistChildren}
        level={level}
        onToggle={setOpen}
        onItemSelect={nestListItemClickAction}
        open={open}
      />

      {open && renderBranches()}
    </>
  );
};

export default NestListBranch;
