import { FC } from 'react';
import { ListItemButton, ListItemText } from '@mui/material';

import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import type { TreeItem } from './NestListTree';

type Props = {
  item: TreeItem;
  isExistChildren: boolean;
  level: number;
  onToggle: (isSelect: boolean) => void;
  onItemSelect: (treeItem: TreeItem) => void;
  open: boolean;
};

const NestListNode: FC<Props> = (props) => {
  const { item, isExistChildren, level, onToggle, onItemSelect, open } = props;

  return (
    <>
      <ListItemButton sx={{ pl: 4 * level }}>
        <ListItemText
          primary={item.value}
          onClick={
            isExistChildren ? () => onToggle(!open) : () => onItemSelect(item)
          }
        />
        {isExistChildren && (open ? <ExpandLess /> : <ExpandMore />)}
      </ListItemButton>
    </>
  );
};

export default NestListNode;
