import { VFC, useEffect, useState } from 'react';
import { Box, CircularProgress, Stack, Typography } from '@mui/material';
import BlockIcon from '@mui/icons-material/Block';
import { red } from '@mui/material/colors';
import ky from '@/lib/ky';
import TransfusionOrder from '../../../types/order/transfusion/transfusionOrder';
import TranfusionExecutePresenter, {
  TransfusionExecuteForm,
} from './TransfusionExecutePresenter';
import TransfusionDeliveryFormulation from '../../../types/order/transfusion/transfusionDeliveryFormulation';

type TransfusionResult = {
  transfusionOrder?: TransfusionOrder;
};

type TransfusionPatchRequest = {
  orderHeaderId: number;
  orderStatus: number;
  version: number;
  updateStaffId: number;
  execute: TransfusionExecutePatchRequest;
};

type TransfusionExecutePatchRequest = {
  formulationCode: string;
  formulationNo: string;
  executeDate: string;
  executeStaffId: number;
  executeType: number;
  filterType: number;
  sideEffectIds: number[];
};

type TransfusionExecutePatchResponse = {
  result: string;
};

type Props = {
  patientId?: string;
  formulationCode?: string;
  formulationNo?: string;
};

const TranfusionExecute: VFC<Props> = ({
  patientId = '2730',
  formulationCode = '0393012',
  formulationNo = '123456',
}) => {
  const [order, setOrder] = useState<TransfusionOrder | undefined>(undefined);
  const [deliveryFormulation, setDeliveryFormulation] = useState<
    TransfusionDeliveryFormulation | undefined
  >(undefined);
  const [isLoading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    const load = async (): Promise<void> => {
      setLoading(true);
      try {
        const transfusionResult = await ky
          .get(`orders/transfusions`, {
            searchParams: {
              patientId,
              formulationCode,
              formulationNo,
            },
          })
          .json<TransfusionResult>();

        setOrder(transfusionResult.transfusionOrder);

        if (transfusionResult.transfusionOrder) {
          const formurations =
            transfusionResult.transfusionOrder.content.deliveryFormulations;
          const targetFormulation = formurations.find(
            (v) =>
              v.formulationCode === formulationCode &&
              v.formulationNo === formulationNo,
          );

          setDeliveryFormulation(targetFormulation);
        }
      } catch (err) {
        // eslint-disable-next-line no-console
        console.log('error');
      } finally {
        setLoading(false);
      }
    };

    void load();
  }, [patientId, formulationCode, formulationNo]);

  // 保存処理
  const handleExecute = async (input: TransfusionExecuteForm) => {
    if (!order) {
      return;
    }

    const { orderHeaderId, orderStatus, version } = order;
    const {
      executeDate,
      executeStaffId,
      executeType,
      filterType,
      sideEffectIds,
    } = input;

    const transfusionOrderPatch: TransfusionPatchRequest = {
      orderHeaderId,
      version,
      orderStatus,
      updateStaffId: 10008,
      execute: {
        formulationCode,
        formulationNo,
        executeDate,
        executeStaffId,
        executeType,
        filterType,
        sideEffectIds,
      },
    };

    try {
      await ky
        .patch(`orders/transfusions`, {
          json: transfusionOrderPatch,
          timeout: false,
        })
        .json<TransfusionExecutePatchResponse>();
    } catch {
      // eslint-disable-next-line no-alert
      alert('error');
    } finally {
      // eslint-disable-next-line no-alert
      alert('finally');
    }
  };

  if (isLoading) {
    return (
      <Box sx={{ display: 'flex' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (!order || !deliveryFormulation) {
    return (
      <Stack
        component="form"
        noValidate
        spacing={0}
        paddingTop="25px"
        sx={{
          flexGrow: 1,
        }}
      >
        <Box sx={{ display: 'flex' }}>
          <BlockIcon sx={{ color: red[500] }} />
          <Typography>オーダが取得できませんでした。</Typography>
        </Box>
        <Typography>患者ID：{patientId}</Typography>
        <Typography>製剤コード：{formulationCode}</Typography>
        <Typography>製剤番号：{formulationNo}</Typography>
      </Stack>
    );
  }

  return (
    <TranfusionExecutePresenter
      transfusionOrder={order}
      deliveryFormulation={deliveryFormulation}
      handleExecute={handleExecute}
    />
  );
};

export default TranfusionExecute;
