import OrderHeader from '../../../types/order/orderHeader';

const getStatusName = (orderStatus: number): string => {
  if (orderStatus === 2) {
    return '未実施';
  }
  if (orderStatus === 3) {
    return '実施中';
  }
  if (orderStatus === 4) {
    return '実施完了';
  }

  return '';
};

const crateStatusSummary = (order: OrderHeader): string => {
  const {
    issueName,
    deputeIssueName,
    issueDate,
    auditName,
    auditDate,
    orderStatus,
  } = order;

  const statusName = getStatusName(orderStatus);

  return `依頼医：${issueName}
    代理入力者：${deputeIssueName}
    依頼日時：${issueDate}
    実施者：${auditName}
    実施日時：${auditDate}
    オーダ状態：${statusName}
    `.replaceAll('null', '');
};

export default crateStatusSummary;
