import { VFC, useEffect, useState } from 'react';
import ky from '@/lib/ky';
import TransfusionExecuteTypeSelectPresenter from './TransfusionExecuteTypeSelectPresenter';
import TransfusionExecuteType from '../../../types/order/transfusion/transfusionExecuteType';

type Props = {
  onChange?: (exutecuteType?: TransfusionExecuteType) => void;
  label?: string;
  value?: number;
  error?: boolean;
  helperText?: string;
  displayExecuteTypes?: ExecuteType[];
};

type ExecuteType = 'DELIVERY' | 'EXECUTE' | 'RETURN' | '';

type TransfusionExecuteTypeResult = {
  executeTypes: TransfusionExecuteType[];
};

const TransfusionExecuteTypeSelect: VFC<Props> = ({
  value,
  label,
  onChange,
  error,
  helperText,
  displayExecuteTypes = ['DELIVERY', 'EXECUTE', 'RETURN'],
}) => {
  const [executeTypes, setExecuteTypes] = useState<TransfusionExecuteType[]>(
    [],
  );

  useEffect(() => {
    const loadStaffs = async (): Promise<void> => {
      try {
        const executeResult = await ky
          .get(`orders/transfusions/executes/types`)
          .json<TransfusionExecuteTypeResult>();

        const filteredExecuteTypes = executeResult.executeTypes.filter(
          (e) => displayExecuteTypes.findIndex((d) => d === e.type) > -1,
        );
        setExecuteTypes(filteredExecuteTypes);
      } catch (err) {
        // eslint-disable-next-line no-console
        console.log('error');
      } finally {
        // setIsLoading(false);
      }
    };

    void loadStaffs();
  }, [displayExecuteTypes]);

  const handleOnChange = (input: string | null): void => {
    const selected = executeTypes.find((type) => String(type.value) === input);

    if (onChange) {
      onChange(selected);
    }
  };

  return (
    <TransfusionExecuteTypeSelectPresenter
      execTypeItems={executeTypes}
      handleOnChange={handleOnChange}
      defaultExecuteType={String(value)}
      label={label}
      error={error}
      helperText={helperText}
    />
  );
};

export default TransfusionExecuteTypeSelect;
