import ky from 'ky';
import { InjectionExecute } from '@/types/order/injection/injectionExecute';

export type InjectionExecutePatch = {
  orderHeaderId: number;
  version: number;
  updateId: number;
  end: boolean;
  execList: InjectionExecute[];
};

export type ExecuteResult = {
  orderHeaderId: number;
  version: number;
  updateId: number;
  end: boolean;
  execList: InjectionExecute[];
};

const doExecution = async (
  execute: InjectionExecutePatch,
): Promise<boolean> => {
  try {
    await ky
      .post(`http://localhost:8080/jerseywebapp/rest/order/injection`, {
        json: execute,
        timeout: false,
      })
      .json<ExecuteResult>();

    return true;
  } catch (err) {
    // eslint-disable-next-line no-alert
    alert(err);

    return false;
  }
};

export default doExecution;
