import { VFC } from 'react';
import List from '@mui/material/List';
import Avatar from '@mui/material/Avatar';
import Drawer from '@mui/material/Drawer';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';

import ShareIcon from '@mui/icons-material/Share';
import PersonIcon from '@mui/icons-material/Person';

import { styled } from '@mui/material/styles';
import { indigo, pink } from '@mui/material/colors';

import pjson from '../../../../package.json';

type Props = {
  drawerOpen: boolean;
  toggleDrawer: () => void;
  onOpen: () => void;
};

const DrawerList = styled('div')(() => ({
  width: 250,
}));

const DrawerHeader = styled('div')(() => ({
  height: 150,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  padding: '1em',
  backgroundColor: indigo[500],
  color: '#ffffff',
}));

const DrawerAvatar = styled(Avatar)(({ theme }) => ({
  backgroundColor: pink[500],
  width: theme.spacing(6),
  height: theme.spacing(6),
}));

const SideBar: VFC<Props> = ({ drawerOpen = false, toggleDrawer, onOpen }) => (
  <Drawer variant="temporary" open={drawerOpen} onClose={toggleDrawer}>
    <DrawerList role="presentation" onClick={toggleDrawer}>
      <DrawerHeader>
        <DrawerAvatar>
          <PersonIcon />
        </DrawerAvatar>
        <p>TODO v{pjson.version}</p>
      </DrawerHeader>
      <List>
        <Divider />
        <ListItem button onClick={onOpen} aria-label="share">
          <ListItemIcon>
            <ShareIcon />
          </ListItemIcon>
          <ListItemText secondary="このアプリを共有" />
        </ListItem>
      </List>
    </DrawerList>
  </Drawer>
);

export default SideBar;
