import { VFC, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useLogout } from '../../../contexts/auth/AuthUserContext';
import TransfusionCheckPresenter from './TransfusionCheckPresenter';

const TransfusionCheck: VFC = () => {
  const [drawerOpen, setDrawerOpen] = useState<boolean>(false);
  const [patientId, setPatientId] = useState<string>('');
  const [formurationCode, setFormurationCode] = useState<string>('');
  const [formurationNumber, setFormurationNumber] = useState<string>('');
  const [inputCode, setInputCode] = useState<string>('');
  const [dialogOpen, setDialogOpen] = useState<boolean>(false);

  const logout = useLogout();
  const navigate = useNavigate();
  const toggleDrawer = () => setDrawerOpen(!drawerOpen);
  const onLogout = () => logout();

  const onScan = (code: string) => {
    setInputCode(code);
  };

  useEffect(() => {
    if (!patientId) {
      setPatientId(inputCode);
    } else if (!formurationCode) {
      setFormurationCode(inputCode);
    } else if (!formurationNumber) {
      setFormurationNumber(inputCode);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inputCode]);

  useEffect(() => {
    if (patientId && formurationCode && formurationNumber) {
      // alert(
      //   `patientId:${patientId}, formurationCode:${formurationCode}, formurationNumber:${formurationNumber}`,
      // );
      setDialogOpen(true);
    }
  }, [formurationCode, formurationNumber, patientId]);

  return (
    <TransfusionCheckPresenter
      drawerOpen={drawerOpen}
      toggleDrawer={toggleDrawer}
      onLogout={onLogout}
      patientId={patientId}
      formurationCode={formurationCode}
      formurationNumber={formurationNumber}
      onScan={onScan}
      dialogOpen={dialogOpen}
      setDialogOpen={setDialogOpen}
      transit={() => navigate('/transfusion')}
    />
  );
};

export default TransfusionCheck;
