import { FC } from 'react';
import { List, ListItem } from '@mui/material/';
import DropExecuteItem from './DropExecuteItem';
import { InjectionOrder } from '@/types/order/injection/injectionOrder';
import { InjectionExecute } from '@/types/order/injection/injectionExecute';

const style = {
  width: '100%',
  maxWidth: 380,
  bgcolor: 'background.paper',
};

type Props = {
  dropItemList: InjectionExecute[];
  deleteAction: (value: InjectionExecute) => void;
  injectionOrder: InjectionOrder;
};

const DropExecuteItemList: FC<Props> = ({
  dropItemList,
  deleteAction,
  injectionOrder,
}) => (
  <>
    <List sx={style} component="nav">
      {dropItemList.map((listItem) => (
        <ListItem>
          <DropExecuteItem
            itemData={listItem}
            onClickDeleteButton={deleteAction}
            itemDataArray={dropItemList}
            isLastRow={
              dropItemList[dropItemList.length - 1].injectionExecutionId ===
              listItem.injectionExecutionId
            }
            injectionOrder={injectionOrder}
          />
        </ListItem>
      ))}
    </List>
  </>
);

export default DropExecuteItemList;
