import { FC, useState } from 'react';
import { Alert, Backdrop, Fab, Grid, Snackbar } from '@mui/material';
import NavigationIcon from '@mui/icons-material/Navigation';
import { styled } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import DropExecuteItemInput from './DropExecuteItemInput';
import { useAuthUser } from '@/contexts/auth/AuthUserContext';
import { InjectionExecute } from '@/types/order/injection/injectionExecute';
import { InjectionOrder } from '@/types/order/injection/injectionOrder';
import type { InjectionExecutePatch } from '../InjectionExecution';
import doExecution from '../InjectionExecution';
import InjectionPatientCard from '../../../oraganisms/InjectionPatientCard/InjectionPatientCard';

type Prop = {
  injectionOrder: InjectionOrder;
};

const FabButton = styled(Fab)({
  position: 'fixed',
  right: 15,
  bottom: 15,
});

const DropExecute: FC<Prop> = ({ injectionOrder }) => {
  const navigate = useNavigate();
  const auth = useAuthUser();
  const [isEndFlg, setEndFlg] = useState(injectionOrder.orderStatus === 4);
  const [executes, setExecutes] = useState<InjectionExecute[]>(
    injectionOrder.execList,
  );
  const [successBarOpen, setSuccessBarOpen] = useState<boolean>(false);
  const [errorBarOpen, setErrorBarOpen] = useState<boolean>(false);

  // 入力画面の初期入力値
  const inputData: InjectionExecute = {
    injectionExecutionId: 0,
    executionStartId: 0,
    executionEndId: 0,
    executionDropSpeed: 0,
  };

  // 点滴履歴一覧の追加処理
  const inputDropRecord = (itemData: InjectionExecute, endFlg: boolean) => {
    setExecutes((e) => [...e, itemData]);
    setEndFlg(endFlg);
  };

  // 点滴履歴一覧の削除処理
  const deleteDropRecord = (itemData: InjectionExecute) => {
    setExecutes((e) => e.filter((f) => f !== itemData));
    setEndFlg(false);
  };

  const executeButtonAction = async () => {
    const executeData: InjectionExecutePatch = {
      orderHeaderId: injectionOrder?.orderHeaderId ?? 0,
      version: injectionOrder?.version ?? 0,
      updateId: auth ? Number(auth.staffId) : 0,
      end: isEndFlg,
      execList: injectionOrder?.orderStatus !== 4 ? executes : [],
    };

    const result = await doExecution(executeData);

    if (result) {
      setSuccessBarOpen(true);
    } else {
      setErrorBarOpen(true);
    }
  };

  return (
    <>
      <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={successBarOpen}
      />
      <Snackbar
        open={successBarOpen}
        autoHideDuration={6000}
        onClose={() => {
          setSuccessBarOpen(false);
          navigate(-1);
        }}
      >
        <Alert
          onClose={() => {
            setSuccessBarOpen(false);
            navigate(-1);
          }}
          severity="success"
          sx={{ width: '100%' }}
        >
          {injectionOrder?.orderStatus !== 4
            ? '実施情報を登録しました。'
            : '実施取消を行いました。'}
        </Alert>
      </Snackbar>

      <Snackbar
        open={errorBarOpen}
        autoHideDuration={6000}
        onClose={() => {
          setErrorBarOpen(false);
        }}
      >
        <Alert
          onClose={() => {
            setErrorBarOpen(false);
          }}
          severity="error"
          sx={{ width: '100%' }}
        >
          実施情報の更新に失敗しました。
        </Alert>
      </Snackbar>

      <FabButton
        variant="extended"
        color="primary"
        aria-label="add-button"
        onClick={executeButtonAction}
      >
        <NavigationIcon sx={{ mr: 1 }} />
        {injectionOrder?.orderStatus !== 4 ? '確定' : '実施取消'}
      </FabButton>

      <Grid container spacing={2} direction="column">
        <Grid item>
          <InjectionPatientCard
            patientId={injectionOrder.patientId}
            injectionOrder={injectionOrder}
            executes={executes}
            deleteExecuteItem={deleteDropRecord}
          />
        </Grid>
        <Grid item />
        <DropExecuteItemInput
          defaultExecute={inputData}
          onInput={inputDropRecord}
          executes={executes}
          totalAmount={injectionOrder.totalAmount}
          isShowRemainAmount
          reserveDate={injectionOrder.reserveDate}
        />
      </Grid>
    </>
  );
};

export default DropExecute;
