import { VFC } from 'react';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import ListItemText from '@mui/material/ListItemText';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import Checkbox from '@mui/material/Checkbox';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

type NameValueItem = {
  name: string;
  value: string;
};

type Props = {
  items: NameValueItem[];
  label?: string;
  values?: string[];
  onChange?: (values: NameValueItem[]) => void;
};

const getSelectedNames = (items: NameValueItem[], selectedValues: string[]) =>
  selectedValues
    .map((selectedValue) => {
      const selectedItem = items.find((item) => item.value === selectedValue);

      if (selectedItem) {
        return selectedItem;
      }

      return { name: '', value: 0 };
    })
    .filter((selectedNameValue) => selectedNameValue.value)
    .map((selectedNameValue) => selectedNameValue.name);

const MultipleSelectPresenter: VFC<Props> = ({
  items,
  label,
  values = [],
  onChange,
}) => {
  const selectedNames = getSelectedNames(items, values);

  const handleChange = (event: SelectChangeEvent<typeof values>) => {
    const {
      target: { value },
    } = event;

    const selectedValues =
      typeof value === 'string' ? [value] : value.slice().sort();
    if (onChange) {
      const selectedNameValues = items.filter(
        (item) => selectedValues.findIndex((v) => v === item.value) > -1,
      );
      onChange(selectedNameValues);
    }
  };

  return (
    <>
      <FormControl sx={{ m: 1, width: 300 }}>
        <InputLabel id="multiple-checkbox-label">{label}</InputLabel>
        <Select
          labelId="multiple-checkbox-label"
          id="multiple-checkbox"
          multiple
          value={values}
          onChange={handleChange}
          input={<OutlinedInput label="Tag" />}
          renderValue={(_) => selectedNames.join(', ')}
          MenuProps={MenuProps}
        >
          {items.map((item) => (
            <MenuItem key={item.name} value={item.value}>
              <Checkbox
                checked={values.findIndex((value) => value === item.value) > -1}
              />
              <ListItemText primary={item.name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </>
  );
};

export default MultipleSelectPresenter;
