import { VFC } from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import {
  Button,
  Card,
  CardActions,
  CardContent,
  Typography,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import LauncherMenuDefinition from './LauncherMenuDefinition.json';

type LauncherMenuProps = {
  displayName: string;
  description: string;
  navigation: string;
};

const menus: LauncherMenuProps[] = LauncherMenuDefinition;

const MenuCardContent: VFC<LauncherMenuProps> = ({
  displayName,
  description,
  navigation,
}) => {
  const navigate = useNavigate();

  return (
    <>
      <CardContent>
        <Typography variant="h5" component="div">
          {displayName}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
          {description}
        </Typography>
      </CardContent>
      <CardActions>
        <Button
          variant="contained"
          size="large"
          onClick={() => navigate(navigation)}
        >
          Start
        </Button>
      </CardActions>
    </>
  );
};

const LauncherMenuList: VFC = () => (
  <Box sx={{ flexGrow: 1 }}>
    <Grid container spacing={1}>
      {menus.map(({ displayName, description, navigation }) => (
        <Grid item xs={6} key={displayName}>
          <Card variant="outlined">
            <MenuCardContent
              displayName={displayName}
              description={description}
              navigation={navigation}
            />
          </Card>
        </Grid>
      ))}
    </Grid>
  </Box>
);

export default LauncherMenuList;
