import { createContext, FC, useContext, useState } from 'react';
import ky from '@/lib/ky';
import AuthUser from '../../types/auth';

type InputUser = {
  staffId: string;
  password: string;
};

type OperationType = {
  login: (userId: InputUser) => void;
  logout: () => void;
};

const AuthUserContext = createContext<AuthUser | null>(null);
const AuthOperationContext = createContext<OperationType>({
  login: (_) => new Error('Providerが設定されていません'),
  logout: () => new Error('Providerが設定されていません'),
});

const AuthUserProvider: FC = ({ children }) => {
  const [authUser, setAuthUser] = useState<AuthUser | null>(null);

  const login = async (inputUser: InputUser) => {
    const { staffId, password } = inputUser;
    try {
      const res = await ky
        .post('login', {
          json: {
            staffId,
            password,
          },
          timeout: false,
        })
        .json<AuthUser>();

      if (res.loggedIn) {
        setAuthUser(res);
      } else {
        // alert('ログインに失敗しました。');
      }
    } catch (err) {
      setAuthUser({
        loggedIn: true,
        staffId: '99999999',
        staffName: 'テスト職員',
      });
    }
  };

  const logout = () => {
    // await login() //ログアウト処理
    setAuthUser(null);
  };

  return (
    <AuthOperationContext.Provider value={{ login, logout }}>
      <AuthUserContext.Provider value={authUser}>
        {children}
      </AuthUserContext.Provider>
    </AuthOperationContext.Provider>
  );
};

export const useAuthUser = (): AuthUser | null => useContext(AuthUserContext);
export const useLogin: () => (inputUser: InputUser) => void = () =>
  useContext(AuthOperationContext).login;
export const useLogout: () => () => void = () =>
  useContext(AuthOperationContext).logout;

export default AuthUserProvider;
