/* eslint-disable no-nested-ternary */
import { FC, useState } from 'react';
import { Button, Grid, Divider, Typography, CardContent } from '@mui/material';

import format from 'date-fns/format';

import DropExecuteItemList from '@/components/pages/InjectionExecute/drop/DropExecuteItemList';
import PatientCard from '@/components/oraganisms/PatientCard/PatientCard';
import { InjectionOrder } from '@/types/order/injection/injectionOrder';
import { InjectionExecute } from '@/types/order/injection/injectionExecute';

type DisplayType = 'summary' | 'status' | 'execute' | '';

type Props = {
  patientId: string;
  injectionOrder: InjectionOrder;
  executes: InjectionExecute[];
  // eslint-disable-next-line react/require-default-props
  deleteExecuteItem?: (itemData: InjectionExecute) => void;
};

const InjectionPatientCard: FC<Props> = ({
  patientId,
  injectionOrder,
  executes,
  deleteExecuteItem,
}) => {
  const [expanded, setExpanded] = useState<boolean>(false);
  const [displayType, setDisplayType] = useState<DisplayType>('');

  const handleExpandClick = (selectedDisplayType: DisplayType) => {
    if (!displayType) {
      setExpanded(!expanded);
      setDisplayType(selectedDisplayType);

      return;
    }

    if (displayType !== selectedDisplayType) {
      setDisplayType(selectedDisplayType);

      if (!expanded) {
        setExpanded(!expanded);
      }

      return;
    }

    setExpanded(!expanded);
  };

  const cardActions: JSX.Element = (
    <Grid container spacing={2}>
      <Grid item xs={4}>
        <Button size="small" onClick={() => handleExpandClick('summary')}>
          {expanded && displayType === 'summary'
            ? 'オーダ内容を閉じる'
            : 'オーダ内容を表示'}
        </Button>
      </Grid>
      <Grid item xs={4}>
        <Button size="small" onClick={() => handleExpandClick('status')}>
          {expanded && displayType === 'status'
            ? '受付内容を閉じる'
            : '受付内容を表示'}
        </Button>
      </Grid>
      {injectionOrder?.execType === 2 && (
        <Grid item xs={4}>
          <Button size="small" onClick={() => handleExpandClick('execute')}>
            {expanded && displayType === 'execute'
              ? '実施内容を閉じる'
              : '実施内容を表示'}
          </Button>
        </Grid>
      )}
    </Grid>
  );

  const plansDateText = () => {
    if (injectionOrder?.plansDate != null) {
      if (injectionOrder.plansDate.substring(11) === '99:99') {
        return format(
          new Date(injectionOrder?.plansDate.substring(0, 10)),
          'yyyy/MM/dd 未定',
        );
      }

      return format(new Date(injectionOrder?.plansDate), 'yyyy/MM/dd HH:mm');
    }

    return '';
  };

  const createStatusSummary = `
  依頼医：${injectionOrder?.issueStaffName ?? ''}
  依頼日時：${
    injectionOrder?.issueDate != null
      ? format(new Date(injectionOrder?.issueDate), 'yyyy/MM/dd HH:mm')
      : ''
  }
  代理入力者：${injectionOrder?.departmentName ?? ''}
  指示受者：${injectionOrder?.instructedName ?? ''}
  指示受日時：${
    injectionOrder?.instructedDate != null
      ? format(new Date(injectionOrder?.instructedDate), 'yyyy/MM/dd HH:mm')
      : ''
  }
  実施者：${injectionOrder?.completeName ?? ''}
  実施日時：${
    injectionOrder?.completeDate != null
      ? format(new Date(injectionOrder?.completeDate), 'yyyy/MM/dd HH:mm')
      : ''
  }
  オーダ状態：${injectionOrder?.orderStatusName ?? ''}
  予定日時：${plansDateText()}`;

  const createCardContent = (inputDisplayType: DisplayType) => {
    if (inputDisplayType === 'summary') {
      return injectionOrder?.summary
        .split(/(\n)/)
        .filter((item) => /\n/.exec(item) === null)
        .map((item) => <Typography>{item}</Typography>);
    }

    if (inputDisplayType === 'status') {
      return createStatusSummary
        .split(/(\n)/)
        .filter((item) => /\n/.exec(item) === null)
        .map((item) => <Typography>{item}</Typography>);
    }

    if (inputDisplayType === 'execute') {
      if (injectionOrder != null && deleteExecuteItem) {
        return (
          <DropExecuteItemList
            dropItemList={executes}
            deleteAction={deleteExecuteItem}
            injectionOrder={injectionOrder}
          />
        );
      }
    }

    return null;
  };

  const cardContent: JSX.Element = (
    <>
      <Divider />
      <CardContent>{createCardContent(displayType)}</CardContent>
    </>
  );

  return (
    <PatientCard
      patientId={patientId}
      cardActions={cardActions}
      cardContent={cardContent}
      displayContent={expanded}
    />
  );
};

export default InjectionPatientCard;
