import { VFC } from 'react';
import { Button, Container, Stack, TextField } from '@mui/material';
import { SubmitHandler, useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';

type Props = {
  onLogin: (staffId: string, password: string) => void;
};

type LoginInput = {
  staffId: string;
  password: string;
  sample: string;
};

const schema = yup.object({
  staffId: yup.string().required('職員IDを入力してください。'),
  password: yup.string().required('パスワードを入力してください。'),
});

const LoginPresenter: VFC<Props> = ({ onLogin }) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<LoginInput>({
    resolver: yupResolver(schema),
  });

  const onSubmit: SubmitHandler<LoginInput> = ({ staffId, password }) => {
    onLogin(staffId, password);
  };

  return (
    <Container maxWidth="sm">
      <Stack spacing={2}>
        <TextField
          id="staff-id"
          label="職員ID"
          type="number"
          required
          defaultValue="10008"
          error={'staffId' in errors}
          helperText={errors.staffId?.message}
          {...register('staffId')}
        />
        <TextField
          id="staff-password"
          label="パスワード"
          type="password"
          required
          defaultValue="Llanowar12$"
          autoComplete="current-password"
          error={'password' in errors}
          helperText={errors.password?.message}
          {...register('password')}
        />
        <Button variant="contained" onClick={handleSubmit(onSubmit)}>
          ログイン
        </Button>
      </Stack>
    </Container>
  );
};

export default LoginPresenter;
