import { VFC, useEffect, useState } from 'react';
import ky from 'ky';
import SideEffectSelectPresenter from './SideEffectSelectPresenter';
import TransfusionSideEffect from '@/types/order/transfusion/transfusionSideEffect';

type NameValueItem = {
  name: string;
  value: string;
};

type Props = {
  label?: string;
  sideEffectIds?: number[];
  onChange?: (values: TransfusionSideEffect[]) => void;
};

type Result = {
  sideEffects: TransfusionSideEffect[];
};

const SideEffectSelect: VFC<Props> = ({
  label = '',
  sideEffectIds = [],
  onChange,
}) => {
  const [sideEffects, setSideEffects] = useState<TransfusionSideEffect[]>([]);
  const [sideEffectItems, setSideEffectItems] = useState<NameValueItem[]>([]);

  useEffect(() => {
    const loadStaffs = async (): Promise<void> => {
      try {
        const result = await ky
          .get(
            `http://localhost:8080/jerseywebapp/rest/orders/transfusions/executes/side-effects`,
          )
          .json<Result>();

        setSideEffects(result.sideEffects);
        setSideEffectItems(
          result.sideEffects.map((s) => {
            const name = s.sideEffectName;
            const value = String(s.sideEffectMstId);

            return { name, value };
          }),
        );
      } catch (err) {
        // eslint-disable-next-line no-console
        console.log('error');
      } finally {
        // setIsLoading(false);
      }
    };

    void loadStaffs();
  }, []);

  const handleOnChange = (items: NameValueItem[]) => {
    const selectedSideEffects = sideEffects.filter(
      (s) => items.findIndex((i) => Number(i.value) === s.sideEffectMstId) > -1,
    );

    if (onChange) {
      void onChange(selectedSideEffects);
    }
  };

  return (
    <SideEffectSelectPresenter
      items={sideEffectItems}
      label={label}
      sideEffectIds={sideEffectIds ? sideEffectIds.map((v) => String(v)) : []}
      onChange={handleOnChange}
    />
  );
};

export default SideEffectSelect;
