import { VFC } from 'react';
import {
  Autocomplete,
  FormControl,
  FormHelperText,
  IconButton,
  Stack,
  TextField,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';

type Props = {
  staffNames: string[];
  handleClickIcon: () => void;
  handleOnChange: (input: string | null) => void;
  staffName?: string;
  label?: string;
  error?: boolean;
  helperText?: string;
  disabled?: boolean;
};

const StaffSelectPresenter: VFC<Props> = ({
  staffNames,
  handleClickIcon,
  handleOnChange,
  staffName,
  label,
  error,
  helperText,
  disabled,
}) => (
  <>
    <FormControl error={error}>
      <Stack spacing={0}>
        <Stack direction="row" spacing={0}>
          <Autocomplete
            id="combo-box-demo"
            options={staffNames}
            value={staffName}
            onChange={(_, newValue: string | null) => handleOnChange(newValue)}
            sx={{ width: 300 }}
            renderInput={(params) => (
              <TextField {...params} error={error} label={label} />
            )}
            disabled={disabled}
          />
          {!disabled && (
            <IconButton
              color="primary"
              component="span"
              onClick={handleClickIcon}
            >
              <AddIcon />
            </IconButton>
          )}
        </Stack>
        <FormHelperText>{helperText}</FormHelperText>
      </Stack>
    </FormControl>
  </>
);
export default StaffSelectPresenter;
