import { FC, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useLogout } from '@/contexts/auth/AuthUserContext';
import InjectionCheckPresenter from './InjectionCheckPresenter';

const InjectionCheck: FC = () => {
  const [drawerOpen, setDrawerOpen] = useState<boolean>(false);
  const [patientId, setPatientId] = useState<string>('');
  const [orderId, setOrderId] = useState<string>('');
  const [inputCode, setInputCode] = useState<string>('');
  const [dialogOpen, setDialogOpen] = useState<boolean>(false);

  const logout = useLogout();
  const navigate = useNavigate();
  const toggleDrawer = () => setDrawerOpen(!drawerOpen);
  const onLogout = () => logout();

  const onScan = (code: string) => {
    const zeroSuppressCode = Number(code);

    setInputCode(String(zeroSuppressCode));
  };

  useEffect(() => {
    if (!patientId) {
      setPatientId(inputCode);
    } else if (!orderId) {
      setOrderId(inputCode);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inputCode]);

  useEffect(() => {
    if (patientId && orderId) {
      setDialogOpen(true);
    }
  }, [orderId, patientId]);

  return (
    <InjectionCheckPresenter
      drawerOpen={drawerOpen}
      toggleDrawer={toggleDrawer}
      onLogout={onLogout}
      patientId={patientId}
      orderId={orderId}
      onScan={onScan}
      dialogOpen={dialogOpen}
      setDialogOpen={setDialogOpen}
      transit={() =>
        navigate(`/safetyInjectionExecute/${patientId}/${orderId}`)
      }
    />
  );
};

export default InjectionCheck;
