export const getAge = (birthday: Date): number => {
  const today = new Date();

  const thisYearsBirthday = new Date(
    today.getFullYear(),
    birthday.getMonth() - 1,
    birthday.getDate(),
  );

  let age = today.getFullYear() - birthday.getFullYear();

  if (today < thisYearsBirthday) {
    age -= 1;
  }

  return age;
};

export default getAge;
