import { FC } from 'react';
import NestListBranch from './NestListBranch';

type Props = {
  treeItems: TreeItem[];
  nestListItemClickAction: (treeItem: TreeItem) => void;
};

export type TreeItem = {
  categoryId: number;
  itemId: number;
  value: string;
  parentId: number;
  children?: TreeItem[];
};

const NestListTree: FC<Props> = (props) => {
  const { treeItems, nestListItemClickAction } = props;

  return (
    <>
      {treeItems.map((item) => (
        <NestListBranch
          key={item.itemId}
          item={item}
          level={0}
          nestListItemClickAction={nestListItemClickAction}
        />
      ))}
    </>
  );
};

export default NestListTree;
