import { AccountCircle } from '@mui/icons-material';
import { Box, Grid, InputAdornment, TextField } from '@mui/material';
import { VFC } from 'react';
import AlertDialog from '../../molecules/ErrorDialog/AlertDialog';
import BarcodeReader from '../../oraganisms/BarcodeReader/BarcodeReader';
import Header from '../../oraganisms/Header/Header';
import SideBar from '../../oraganisms/SideBar/SideBar';

type Props = {
  drawerOpen: boolean;
  toggleDrawer: () => void;
  onLogout: () => void;
  patientId: string;
  formurationCode: string;
  formurationNumber: string;
  onScan: (code: string) => void;
  dialogOpen: boolean;
  setDialogOpen: (dialogOpen: boolean) => void;
  transit: () => void;
};

const TransfusionCheckPresenter: VFC<Props> = ({
  drawerOpen,
  patientId,
  formurationCode,
  formurationNumber,
  dialogOpen,
  toggleDrawer,
  onLogout,
  onScan,
  setDialogOpen,
  transit,
}) => (
  <>
    <Header onDrawerToggle={toggleDrawer} />
    <SideBar
      drawerOpen={drawerOpen}
      toggleDrawer={toggleDrawer}
      onOpen={onLogout}
    />
    <AlertDialog
      alertOpen={dialogOpen}
      message={`患者ID：${patientId}
      製剤コード：${formurationCode}
      製剤番号：${formurationNumber}`}
      toggleAlert={() => {
        setDialogOpen(false);
      }}
      onEmpty={() => {
        setDialogOpen(false);
        transit();
      }}
    />
    <Box
      paddingTop="25px"
      sx={{
        flexGrow: 1,
      }}
    >
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <TextField
            id="patient-id"
            label="患者ID"
            color="success"
            value={patientId}
            focused={!!patientId}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AccountCircle />
                </InputAdornment>
              ),
            }}
            fullWidth
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            id="formuration-code"
            label="製剤コード"
            color="success"
            value={formurationCode}
            focused={!!formurationCode}
            fullWidth
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            id="formuration-number"
            label="製剤番号"
            color="success"
            value={formurationNumber}
            focused={!!formurationNumber}
            fullWidth
          />
        </Grid>
        <Grid item xs={12}>
          <BarcodeReader onScan={onScan} />
        </Grid>
      </Grid>
    </Box>
  </>
);

export default TransfusionCheckPresenter;
