/* eslint-disable no-nested-ternary */
import { FC, useCallback, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import ky from 'ky';

import { isAfter } from 'date-fns';
import OneShotExec from './oneshot/OneShotExec';
import DropExecute from './drop/DropExecute';
import Header from '../../oraganisms/Header/Header';
import { InjectionOrder } from '@/types/order/injection/injectionOrder';
import AlertDialog from '@/components/molecules/ErrorDialog/AlertDialog';
import { useAuthUser } from '@/contexts/auth/AuthUserContext';

const InjectionExecute: FC = () => {
  const auth = useAuthUser();
  const { patientid, orderid } = useParams();

  const [injectionOrderData, setInjectionOrderData] =
    useState<InjectionOrder>();

  const [doInstructResult, setDoInstructResult] = useState<boolean>(false);
  const [checkPass, setCheckPass] = useState(false);

  const [errorText, setErrorText] = useState<string | undefined>(undefined);
  const [dialogOpen, setDialogOpen] = useState<boolean>(false);

  const load = useCallback(async (): Promise<void> => {
    try {
      const result = await ky
        .get('http://localhost:8080/jerseywebapp/rest/order/injection', {
          searchParams: {
            orderId: Number(orderid),
          },
        })
        .json<InjectionOrder>();

      const InputBarcodeCheck = (): boolean => {
        if (!result) {
          setErrorText('オーダを取得できません。');

          return false;
        }

        if (result.patientId !== patientid) {
          setErrorText('患者情報が一致しません。');

          return false;
        }

        if (result.mainClass !== 'injection') {
          setErrorText('注射オーダを取得できません。');

          return false;
        }

        if (result.orderStatusName === '未受付') {
          setErrorText('このオーダは受付されていません。');

          return false;
        }

        if (result.orderStatus === 5) {
          setErrorText('このオーダは中止されています。');

          return false;
        }

        if (!result.executableFutureDay) {
          const preferredDate = new Date(result.preferredDate);
          if (isAfter(preferredDate, new Date())) {
            setErrorText('未来日は実施できません。');

            return false;
          }
        }

        if (result.instructedStatus === 1) {
          if (result.forcingInstracted) {
            setDialogOpen(true);

            if (!doInstructResult) {
              setErrorText('指示受けされていません。');

              return false;
            }
          } else {
            setErrorText('指示受けされていないため実施は行えません。');

            return false;
          }
        }

        return true;
      };

      setCheckPass(InputBarcodeCheck);
      setInjectionOrderData(result);
    } catch (err) {
      throw new Error('error');
    }
  }, [doInstructResult, orderid, patientid]);

  useEffect(() => {
    void load();
  }, [load]);

  if (!checkPass || !injectionOrderData) {
    return <p>{errorText}</p>;
  }

  const doInstruct = async (
    injectionOrder: InjectionOrder,
  ): Promise<boolean> => {
    try {
      const result = await ky
        .put(`http://localhost:8080/jerseywebapp/rest/order/injection`, {
          json: {
            orderHeaderId: injectionOrder.orderHeaderId,
            userId: auth?.staffId,
            patientId: injectionOrder.patientId,
          },
          timeout: false,
        })
        .json<InjectionOrder>();

      setInjectionOrderData(result);

      return true;
    } catch {
      // eslint-disable-next-line no-alert
      alert('error');

      return false;
    }
  };

  return (
    <>
      <Header
        onDrawerToggle={() => {
          // eslint-disable-next-line no-console
          console.log(123);
        }}
      />

      <AlertDialog
        alertOpen={dialogOpen}
        message={`指示受けされていません。
        指示受を行いますか？`}
        toggleAlert={() => {
          setDialogOpen(false);
        }}
        onEmpty={async () => {
          setDialogOpen(false);
          setDoInstructResult(await doInstruct(injectionOrderData));
        }}
      />

      {injectionOrderData.execType === 2 ? (
        <DropExecute injectionOrder={injectionOrderData} />
      ) : (
        <OneShotExec injectionOrder={injectionOrderData} />
      )}
    </>
  );
};

export default InjectionExecute;
